import React, { Component } from 'react'

import { CalcType } from './CalcType'

import Select from 'react-select'

export default class CalcSetting extends Component {

  constructor(props) {
    super(props)

    this.options = []
    Object.keys(CalcType).map(key => this.options.push(CalcType[key]))
  }

  handleChange = selectedOption => {
    this.props.setCalcType(selectedOption.value)
  }

  render() {
    const selectedOption = CalcType[this.props.calcType]

    return (
      <span style={{width: 150}}>
        <Select 
            value={selectedOption}
            onChange={this.handleChange}
            options={this.options}
        />
      </span>
    )
  }
}
