export const c_SPADES = 2
export const c_DIAMONDS = 3
export const c_SUIT_SIZE = 4
export const c_SUIT_BAD = -1
export const c_SUIT_PLAYERS = -2
export const c_SUIT_UNKNOWN = 4

export const c_2 = 0
export const c_3 = 1
export const c_4 = 2
export const c_5 = 3
export const c_6 = 4
export const c_7 = 5
export const c_8 = 6
export const c_9 = 7
export const c_T = 8
export const c_J = 9
export const c_Q = 10
export const c_K = 11
export const c_A = 12
export const c_RANK_SIZE = 13
export const c_RANK_BAD = -1
export const c_RANK_UNKNOWN = -2

export const c_TWO = 0
export const c_DEUCE = 0
export const c_THREE = 1
export const c_FOUR = 2
export const c_FIVE = 3
export const c_SIX = 4
export const c_SEVEN = 5
