export const c_deck = "🂠"
export const c_joker = "🃏"
export const c_whiteJoker = "🃟"

// Card codes:
// 
// -19 - wrong rank and suite
export const c_badCard = -19
// -15 - 18 wrong rank, correct suite
export const c_badClub = -18
// -2 - -14 correct rank, wrong suite
export const c_badDeuce = -14
// -1 - unknown card, a wildcard, empty
export const c_unknownCard = -1
// 0-51 - valid cards
// 52-64 - unknown suit
// 65 - any club
export const c_anyClub = 65
// 66 - any diamond
// 67 - any heart
// 68 - any spade
export const c_2p = 69
export const c_3p = 70
export const c_4p = 71
export const c_5p = 72
export const c_6p = 73
export const c_7p = 74
export const c_8p = 75
export const c_9p = 76
export const c_maxCode = 77

export const codeIsNormal = (code) => (0 <= code && code < c_CARD_SIZE)
export const codeIsPlayers = (code) => (c_2p <= code && code <= c_9p)

// Turns card code into ASCII representation
export const code2text = (code) => {
    let text
    switch(code) {
        case c_badCard: {
            text = "??"
            break;
        }
        case c_badClub: {
            text = "?c"
            break;
        }
        case c_badClub+1: {
            text = "?d"
            break;
        }
        case c_badClub+2: {
            text = "?h"
            break;
        }
        case c_badClub+3: {
            text = "?s"
            break;
        }
        case c_unknownCard: {
            text = "__"
            break;
        }
        case c_anyClub: {
            text = "Xc"
            break;
        }
        case c_anyClub+1: {
            text = "Xd"
            break;
        }
        case c_anyClub+2: {
            text = "Xh"
            break;
        }
        case c_anyClub+3: {
            text = "Xs"
            break;
        }
        case c_2p: {
            text = "2p"
            break;
        }
        case c_3p: {
            text = "3p"
            break;
        }
        case c_4p: {
            text = "4p"
            break;
        }
        case c_5p: {
            text = "5p"
            break;
        }
        case c_6p: {
            text = "6p"
            break;
        }
        case c_7p: {
            text = "7p"
            break;
        }
        case c_8p: {
            text = "8p"
            break;
        }
        case c_9p: {
            text = "9p"
            break;
        }

        default: {
            break;
        }
    }

    if(!text) {
        if(c_badDeuce <= code && code < c_badDeuce + c_RANK_SIZE) {
            const rank = code - c_badDeuce
            text = c_ranks[rank] + "?"
        } else if(code < 0 || code >= c_anyClub) {
            text = "??"
        } else {
            const rank = code % c_RANK_SIZE
            const suit = Math.floor(code / c_RANK_SIZE)

            text = c_ranks[rank] + c_suits[suit]
        }
    }

    return text
}

export const code2symbol = (code) => {
    if(code < 0 || code >= c_symbols[code]) {
        return c_deck
    }

    return c_symbols[code]
}

export const code2html = (code, vertical) => {
    let comp
    switch(code) {
        case c_badCard: {
            comp = <>?{vertical?<br/>:null}?</>
            break;
        }
        case c_badClub: {
            comp = <>?{vertical?<br/>:null}&clubs;</>
            break;
        }
        case c_badClub+1: {
            comp = <>?{vertical?<br/>:null}&diams;</>
            break;
        }
        case c_badClub+2: {
            comp = <>?{vertical?<br/>:null}&hearts;</>
            break;
        }
        case c_badClub+3: {
            comp = <>?{vertical?<br/>:null}&spades;</>
            break;
        }
        case c_unknownCard: {
            comp = <>_{vertical?<br/>:null}_</>
            break;
        }
        case c_anyClub: {
            comp = <>{vertical?<br/>:null}&clubs;</>
            break;
        }
        case c_anyClub+1: {
            comp = <>{vertical?<br/>:null}&diams;</>
            break;
        }
        case c_anyClub+2: {
            comp = <>{vertical?<br/>:null}&hearts;</>
            break;
        }
        case c_anyClub+3: {
            comp = <>{vertical?<br/>:null}&spades;</>
            break;
        }
        case c_2p:
        case c_3p:
        case c_4p:
        case c_5p:
        case c_6p:
        case c_7p:
        case c_8p:
        case c_9p:
        {
            comp = <>
                {vertical?String.fromCharCode('2'.charCodeAt(0)+(code-c_2p)):null}
                {vertical?<br/>:null}
                {vertical?<FontAwesomeIcon icon="user"/>: circled[2+code-c_2p]}
            </>
            break;
        }
        default: {
            break;
        }
    }

    if(comp) {
        // we are good
    } else if(0 <= code && code < c_CARD_SIZE + c_RANK_SIZE) {

        const rank = code % c_RANK_SIZE
        const suit = Math.floor(code / c_RANK_SIZE)

        switch(suit) {
            case 0:
                comp = <>{c_textRanks[rank]}{vertical?<br/>:null}&clubs;</>
                break
            case 1:
                comp = <>{c_textRanks[rank]}{vertical?<br/>:null}&diams;</>
                break
            case 2:
                comp = <>{c_textRanks[rank]}{vertical?<br/>:null}&hearts;</>
                break
            case 3:
                comp = <>{c_textRanks[rank]}{vertical?<br/>:null}&spades;</>
                break
            case 4:
                comp = <>{c_textRanks[rank]}{vertical?<br/>:null}x</>
                break
            default:
                // Should not be here
                break
        }
    } else if(c_badDeuce <= code && code < c_badDeuce + c_RANK_SIZE) {
        const rank = code - c_badDeuce
        comp = <>{c_textRanks[rank]}{vertical?<br/>:null}?</>
    } else {
        comp = <>?{vertical?<br/>:null}?</>       
    }
    return comp
}

export const code2color = (code) => {
    let color

    if(code < 0 || code >= c_maxCode) {
        color = "grey"
    } else if(code < c_CARD_SIZE + c_RANK_SIZE) {
        const suit = Math.floor(code / c_RANK_SIZE)
        color = c_suitColors[suit]
    } else if(c_anyClub <= code && code <= c_anyClub+3) {
        const suit = code - c_anyClub
        color = c_suitColors[suit]
    } else if(c_2p <= code && code <= c_9p) {
        color = "blue"
    } else if(code < c_maxCode) {
        color = "grey"
    } else {
        // should not get here
    }

    return color
}

export const text2code = (card) => {
    if(card.length !== 2) {
        return -1
    }

    const c1 = card.charAt(0)
    const c2 = card.charAt(1)

    let rank
    switch(c1) {
        case '2':
        case '3':
        case '4':
        case '5':
        case '6':
        case '7':
        case '8':
        case '9':
            rank = c1 - '2'
            break
        case 't':
        case 'T':
            rank = 8
            break;
        case 'j':
        case 'J':
            rank = 9
            break;
        case 'q':
        case 'Q':
            rank = 10
            break;
        case 'k':
        case 'K':
            rank = 11
            break;
        case 'a':
        case 'A':
            rank = 12
            break;
        case '_':
        case '*':
        case 'x':
        case 'X':
            rank = c_RANK_UNKNOWN;
            break;
        default:
            rank = c_RANK_BAD
            break
    }

    let suit
    switch(c2) {
        case 'c':
        case 'C':
            suit = 0
            break
        case 'd':
        case 'D':
            suit = 1
            break
        case 'h':
        case 'H':
            suit = 2
            break
        case 's':
        case 'S':
            suit = 3
            break
        case '_':
        case '*':
        case 'x':
        case 'X':
            suit = c_SUIT_UNKNOWN
            break
        case 'p':
        case 'P':
            suit = c_SUIT_PLAYERS
            break;
        default:
            suit = c_SUIT_BAD
            break
    }

    let code 
    if(rank >= 0 && suit >= 0) {
        code = suit * c_RANK_SIZE + rank
    } else if(suit === c_SUIT_PLAYERS) {
        if(c_2 <= rank && rank <= c_9) {
            code = c_2p + rank
        } else {
            code = c_badCard
        }
    } else if(rank < 0 && 0 <= suit && suit < 4) {
        if(rank === c_RANK_UNKNOWN) {
            code = c_anyClub + suit
        } else {
            code = c_badClub + suit
        }
    } else if(rank === c_RANK_UNKNOWN && suit === c_SUIT_UNKNOWN) {
        code = c_unknownCard
    } else if(rank >= 0 && suit < 0) {
        code = c_badDeuce + rank
    } else {
        code = c_badCard
    }

    return code
}
