import React, { Component } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import PlayersSetting from '../Settings/components/PlayersSetting'

import css from './InputButtons.module.css'

export default class InputButtons extends Component {

  clear(e) {
    this.props.clear()
  }

  del(e) {
    this.props.del()
  }

  recalc(e) {
    this.props.recalc()
  }

  addRandom(e) {
    this.props.addRandom()
  }

  render() {
    const props = this.props
    const settings = props.settings

    return <div>
      {props.tanking}
      <button title="Clear cards" onClick={this.clear.bind(this)}><FontAwesomeIcon icon="trash-alt"/></button>
      <button title="Delete the rightmost card" onClick={this.del.bind(this)}><FontAwesomeIcon icon="backspace"/></button>
      <button title="Add random cards" onClick={this.addRandom.bind(this)}><FontAwesomeIcon icon="random"/></button>
      &nbsp;
      {settings.get("playersButtons") ?  null : <>
      <PlayersSetting players={props.settings.get("players")} incPlayers={props.incPlayers} decPlayers={props.decPlayers}>Players:</PlayersSetting>
      </>}
      <button title="Recalcuate" className={props.tanking?css.running:""} onClick={this.recalc.bind(this)}><FontAwesomeIcon icon="calculator"/></button>
    </div>
  }

}
