import React, { Component } from 'react'

export default class ServerUrlSetting extends Component {

  changeApiServer = event => {
    this.props.setApiServer(event.target.value)
  }

  render() {
    return (
      <span style={{width: 150}}>
        <input 
          style={{height: 30, width: 140, textAlign: "left"}} 
          type="text" 
          name="ApiServer"
          title="REST Server URL"
          value={this.props.apiServer}
          maxLength={64}
          onChange={this.changeApiServer.bind(this)}
          placeholder="REST Server URL ..."
        />
      </span>
    )
  }
}
