import { text2code, c_2p, c_9p } from './cards'

import { List } from 'immutable-sorted'

export const chopHand = (hand, n) => {
    const arr = []
    hand = hand.trim().toLowerCase()
    while(hand.length > 0) {
      if(hand.length === 1) {
          hand = hand + "_"
      }
      const code = text2code(hand.substr(0, 2))
      arr.push(code)
      hand = hand.substring(2)

      hand = hand.trim()
    }
    while(arr.length < n) {
      arr.push(-1)
    }
    return List(arr)
}

export const getValidCardsCnt = (list) => {
    let n = 0

    for(let i = 0; i < list.size; i++) {
        const c = list.get(i)
        if(0 <= c && c < 52) {
            n++
        }
    }
    return n
}

export const isValidHand = (list) => {
    let nomore = false
    let n = 0

    for(let i = 0; i < list.size; i++) {
        const c = list.get(i)
        if(0 <= c && c < 52) {
            n++
            if(nomore) {
                return false
            }
        } else if(c_2p <= c && c <= c_9p) {
        } else if(c === -1) {
            nomore = true
        } else {
            return false
        }
    }
    return n > 0
}
