import { connect } from 'react-redux'

import { engine, getState } from '../../engine'

import { chopHand, isValidHand } from '../../utils/hand'

import { text2code, code2text, c_CARD_SIZE, c_2p, c_9p } from '../../utils/cards'

import splice from '../../utils/splice'

import { PoserWorkersApi } from '../../api/poserWorkersApi'
import { Deck } from '../../calc/deck';

import { fromJS, OrderedMap } from 'immutable-sorted'

import '../../utils/wasm_exec'

const e = engine.subEngine('home')
const af = e.actionFactory();
const se = e.subEngine('settings')
const saf = se.actionFactory();

const mapStateToProps = (state, repoHead, ownProps) => ({
  hand: state.get("hand"),
  handArr: state.get("handArr"),
  handValid: state.get("handValid"),
  handEval: state.get("handEval"),
  handEvalPlayers: state.get("handEvalPlayers"),
  handResult: state.get("handResult"),
  handCombos: state.get("handCombos"),
  handCardsCnt: state.get("handCardsCnt"),
  err: state.get("err"),
  currentSuit: state.get("currentSuit"),
  settings: state.get("settings"),
  tanking: state.get("tanking"),
  tankingMs: state.get("tankingMs"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), e.head(repo), ownProps)

let recalcPending = false
let poserApi

const runHand = async (dispatch, hand) => {
  const settings = se.get(getState())
  const apiType = settings.get("apiType")
  const server = settings.get("apiServer")
  const calcType = settings.get("calcType")
  const nSims = settings.get("nSims")
  const workers = settings.get("workers")
  const jstSecret = settings.get("jwtSecret")
  const players = settings.get("players")
  const threads = settings.get("threads")
  const playersChart = settings.get("playersChart")
  const playersTable = settings.get("playersTable")
  const tanking = e.get(getState(), "tanking")

  if(tanking) {
    // Don't run if other calculation is in progress
    recalcPending = true
    return
  }

  if(!poserApi || poserApi.workers !== workers) {
    poserApi = new PoserWorkersApi(workers, jstSecret)
  }

  try {
    const startTimeMs = Date.now()
    let err = ""
    dispatch(af.value("tanking", true))
    const apiOptions = apiType === "rest" ? {apiType, server, threads} : {apiType}
    const handCalcResp = await poserApi.HandCalc(apiOptions, hand, calcType)
    if(handCalcResp.err) {
      err = handCalcResp.err
    } else {
      const handCombos = handCalcResp.handCombos
      dispatch(af.value("handCombos", handCombos))
      delete handCalcResp.handCombos
      dispatch(af.value("handResult", handCalcResp))

      let handEvalResult = await poserApi.HandEval(apiOptions, nSims, players, hand, calcType)
      if(handEvalResult.err) {
        err = handEvalResult.err
      } else {
        handEvalResult = fromJS(handEvalResult)
        let handEvalPlayers = OrderedMap()
        dispatch(af.value("handEval", handEvalResult))
        if(playersChart || playersTable) {
          const handEvalPlayersResp = await poserApi.HandEvalPlayers(apiOptions, nSims, hand, calcType)
          if(handEvalPlayersResp.err) {
            err = handEvalPlayersResp.err
          } else {
            for(let i = 0; i < handEvalPlayersResp.handEvals.length; i++) {
              let handEvalResultN = handEvalPlayersResp.handEvals[i]
              const playersN = handEvalResultN.finalPlayers
              handEvalResultN = fromJS(handEvalResultN)
              handEvalPlayers = handEvalPlayers.set(playersN, handEvalResultN)
            }
          }
        }
        dispatch(af.value("handEvalPlayers", handEvalPlayers))
      }
    }
    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
    const endTimeMs = Date.now()
    dispatch(af.value("tankingMs", endTimeMs - startTimeMs))

    if(recalcPending) {
      recalcPending = false;
      recalc(dispatch)
    }
  } catch(e) {
    const err = e.toString()
    dispatch(af.value("err", err))
    dispatch(af.value("tanking", false))
    if(recalcPending) {
      recalcPending = false;
      recalc(dispatch)
    }
  }
}

const setHand = (dispatch, hand) => {
  dispatch(af.value("hand", hand))
  if(isValidHand(chopHand(hand, 7))) {
    runHand(dispatch, hand)
  } else {
    dispatch(af.value("handResult", {}))
  }
}

const recalc = (dispatch) => {
  const hand = e.get(getState(), "hand")
  if(hand.length === 0) {
    return
  }
  setHand(dispatch, hand)
}

const addCards = (dispatch, cards) => {
  const state = getState()
  const origHand = e.get(state, "hand")
  const cardsCnt = e.get(state, "handCardsCnt")

  let selectionStart = origHand.length
  let selectionEnd = selectionStart
  let hand = origHand

  for(let i = 0; i < cards.length; i++) {
    let card = cards[i]
    const n = hand.indexOf(card)
    const code = text2code(card)
    if(n >= 0) {
      selectionStart = n
      selectionEnd = n+card.length
      if(c_2p <= code && code <= c_9p) {
        card = ""
      } else {
        card="__"
      }
    } else if(cardsCnt >= 7 && !(c_2p <= code && code <= c_9p)) {
      continue
    } else {
      const m = hand.indexOf("__")
      if(m >= 0) {
        selectionStart = m
        if(c_2p <= code && code <= c_9p) {
          selectionEnd = selectionStart
        } else {
          selectionEnd = selectionStart + 2
        }
      }
    }
    hand = splice(hand, selectionStart, selectionEnd - selectionStart, card)
  }
  setHand(dispatch, hand)
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  setHand: (hand) => setHand(dispatch, hand),

  runHand: (hand) => runHand(dispatch, hand),

  incPlayers: () => {
    const hand = e.get(getState(), "hand")
    const players = se.get(getState(), "players")
    if(players < 2) {
      dispatch(saf.value("players", 2))
      setHand(dispatch, hand)
    } else if(players < 9) {
      dispatch(saf.value("players", players+1))
      setHand(dispatch, hand)
    }
  },

  decPlayers: () => {
    const hand = e.get(getState(), "hand")
    const players = se.get(getState(), "players")
    if(players > 9) {
      dispatch(saf.value("players", 9))
      setHand(dispatch, hand)
    } else if(players > 2) {
      dispatch(saf.value("players", players-1))
      setHand(dispatch, hand)
    }
  },

  clear: () => {
    setHand(dispatch, "______________")
  },

  recalc: () => {
    recalc(dispatch)
  },

  del: () => {
    const hand = e.get(getState(), "hand")
    if(hand.length === 0) {
      return
    }
    let selectionStart = hand.length
    while(hand.substr(selectionStart-1, 1) === "_") {
      selectionStart--
    }

    let text = "__"
    if(selectionStart > 0) {
      let n = 1
      if(selectionStart >= 2) {
        const card = text2code(hand.substr(selectionStart-2, 2))
        if((0 <= card && card < c_CARD_SIZE) || card === -1) {
          n = 2
        } else if(c_2p <= card && card <= c_9p) {
          n = 2
          text = ""
        }
      }
      const newHand = splice(hand, selectionStart-n, n, text)
      setHand(dispatch, newHand)
    }
  },

  addCard: (card) => {
    addCards(dispatch, [card])
  },

  addRandom: () => {
    const deck = new Deck()
    const handArr = e.get(getState(), "handArr")
    let n = 0
    for(let i = 0; i < handArr.size; i++) {
      const card = handArr.get(i)
      if(0 <= card && card < c_CARD_SIZE) {
        deck.removeCard(card)
        n++
      }
    }
    const cards = []
    for(let i = n; i < 7; i++) {
      const card = code2text(deck.getNextCard())
      cards.push(card)
    }
    addCards(dispatch, cards)
  },

  setCurrentSuit: (suite) => {
    dispatch(af.value("currentSuit", suite))
  },

})

const HomeContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default HomeContainer
