// @flow

import type { Card, CardRank, CardSuit } from './card'
import { MakeCardStr, CardBad, CardWildCard, MakeCard, GetCardStr, GetRank, GetSuit, Card2Players, Card9Players } from './card'

export type Hand7 = {
	ranks: number,
	suits: number,
}

export const Hand7Null: Hand7 = {
	ranks: 0x0FFFFFFF,
	suits: 0x0,
}
export const Hand7Bad: Hand7 = {
	ranks: 0x0FFFFFFF,
	suits: 0x3FFF,
}

export function MakeHand7(): Hand7 {
	return {
		ranks: Hand7Null.ranks,
		suits: Hand7Null.suits,
	}
}

const handRankMasks: Array<number> = [
	~0x0000000F,
	~0x000000F0,
	~0x00000F00,
	~0x0000F000,
	~0x000F0000,
	~0x00F00000,
	~0x0F000000,
]

const handSuitMasks: Array<number> = [
	~0x0003,
	~0x000C,
	~0x0030,
	~0x00C0,
	~0x0300,
	~0x0C00,
	~0x3000,
]

export function StringToHand7(s: string): [Hand7, string] {
	if(!s || s.length < 14) {
		return [Hand7Bad, "not enough cards"]
	}

	let hand: Hand7 = MakeHand7()
	let mask1: number = 0
	let mask2: number = 0
	let m = 0

	for(let i = 0; i < s.length/2; i++) {
		const n: number = i << 1
		const cardStr: string = s.substr(n, 2)
		const card: Card = MakeCardStr(cardStr)

		if(card === CardBad) {
			return [Hand7Bad, ("bad card: " + cardStr)]
		}

		if(!(Card2Players <= card && card <= Card9Players)) {
			if(card === CardWildCard) {
				// don't test for duplicate
			} else if(card >= 32) {
				const bit: number = 1 << (card-32)
				if((mask2 & bit) === 0) {
					mask2 |= bit
				} else {
					return [Hand7Bad, ("duplicate card: " + cardStr)]
				}
			} else {
				const bit: number = 1 << card
				if((mask1 & bit) === 0) {
					mask1 |= bit
				} else {
					return [Hand7Bad, ("duplicate card: " + cardStr)]
				}
			}
			hand = SetHand7Card(hand, m, card)
			m++
		}
	}

	return [hand, ""]
}

export function SetHand7Card(hand: Hand7, n: number, card: Card): Hand7 {
	hand.ranks = (hand.ranks & handRankMasks[n]) | (GetRank(card) << (n << 2)) 
	hand.suits = (hand.suits & handSuitMasks[n]) | (GetSuit(card) << (n << 1))
	return hand
}

export function GetHand7Rank(hand: Hand7, n: number): CardRank {
	return (hand.ranks >> (n << 2)) & 0xF
}

export function GetHand7Suit(hand: Hand7, n: number): CardSuit {
	return (hand.suits >> (n << 1)) & 3
}

export function GetHand7Card(hand: Hand7, n: number): Card {
	const rank = GetHand7Rank(hand, n)
	const suit = GetHand7Suit(hand, n)
	const card = MakeCard(rank, suit)
	return card
}

export function GetHand7HoleStr(hand: Hand7): string {
	const card1 = GetHand7Card(hand, 0)
	const card2 = GetHand7Card(hand, 1)
	const card1Str = GetCardStr(card1)
	const card2Str = GetCardStr(card2)
	return card1Str + card2Str
}

export function GetHand7Str(hand: Hand7): string {
	let s = ""
	for(let i = 0; i < 7; i++) {
		const card = GetHand7Card(hand, i)
		s += GetCardStr(card)
	}
	return s
}
