// @flow

import type { Rng } from './rng'
import type { Card } from "./card"

import { Sfc32 } from "./sfc32"
import { CardSize, CardWildCard, CardNull } from "./card"

export class Deck {
    mask0: number
    mask1: number
    rng: Rng

	constructor()
	{
        this.mask0 = 0
        this.mask1 = 0
        this.rng = new Sfc32()
	}
    
	mix(): void	{
		this.mask0 = 0;
		this.mask1 = 0;
	}
	
	removeCard(card: Card): void {
		this.setMask(card)
	}

	setMask(card: Card): void {
		if(card !== CardWildCard) {
			if(card < 32) {
				this.mask0 |= 1 << card
			} else {
				this.mask1 |= 1 << (card - 32)
			}
		}
	}
	
	getMask(card: Card): number {
        if(card < 32) {
            return this.mask0 & (1 << card)
        } else {
            return this.mask1 & (1 << (card - 32))
        }
	}
	
	// returns the value of the card
	// lower 2 bits are suite, upper bits are value from 0 - 12
	getNextCard(): Card {
		// get next card
		while(this.rng) {
			// generate a random number between 0 an 63
			const rn: number = this.rng.next()
			const n: number = rn & 0x3F

			if(n < CardSize && this.getMask(n) === 0) {
				this.setMask(n)
				return n
			}
		}
		return CardNull
	}
}
