// @flow

import { Base64 } from 'js-base64'
import jwt from 'jsonwebtoken'

import type { CalcType, PoserApiInterface, ApiOptions, HandCalcResp, HandEvalResp, HandEvalPlayersResp } from "./poserApi"

export class RestApi implements PoserApiInterface {

    secret: string

    constructor(secret: string) {
        this.secret = secret
    }

    async getToken(username: string): Promise<string> {
        const payload = {
            user: {username}
        }

        const options = {
            algorithm: 'HS256',
            expiresIn: '1h'
        }

        const secretStr = Base64.decode(this.secret)
        const secret = Buffer.from(secretStr, 'utf8')
        return new Promise<string>((resolve, reject) =>
            jwt.sign(payload, secret, options, (err, token) => {
                if(err) reject(err)
                else resolve(token)
            })
        )
    }

    async fetch(url: string): Promise<Response> {
        const authToken = await this.getToken("john")
        const response = await fetch(url, {
            method: 'GET',
            headers: {
              'Authorization': 'Bearer ' + authToken
            }
        })

        return response
    }

    async HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp> {
        if(apiOptions.apiType !== "rest") {
            throw new Error("invalid apiType in rest code: "+apiOptions.apiType)
        }
        const url: string = apiOptions.server+'/v1/api/calc/'+handStr+"?calc="+calcType
        const response = await this.fetch(url)
        if(response.status !== 200) {
            throw new Error(response.statusText)
        }
        const json = await response.json()
        return json
    }
	
    async HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp> {
        if(apiOptions.apiType !== "rest") {
            throw new Error("invalid apiType in rest code: "+apiOptions.apiType)
        }
        const url: string = apiOptions.server+'/v1/api/eval/'+handStr+"?calc="+calcType+"&nsims="+nsims.toString()+"&players="+players.toString()+"&threads="+apiOptions.threads.toString()
        const response = await this.fetch(url)
        if(response.status !== 200) {
            throw new Error(response.statusText)
        }
        const json = await response.json()
        return json
    }

    async HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp> {
        if(apiOptions.apiType !== "rest") {
            throw new Error("invalid apiType in rest code: "+apiOptions.apiType)
        }
        const url: string = apiOptions.server+'/v1/api/eval/'+handStr+"?calc="+calcType+"&nsims="+nsims.toString()+"&threads="+apiOptions.threads.toString()
        const response = await this.fetch(url)
        if(response.status !== 200) {
            throw new Error(response.statusText)
        }
        const json = await response.json()
        return json
    }

}
