import getBrowserName from './getBrowserName'

function hasAsync() {
    try {
        // eslint-disable-next-line no-new-func
        new Function('async () => {}')();
    } catch (error) {
        return false
    }

    return true
}

export default function getBrowserFeatures() {
    const bf = {
        name: getBrowserName(),
        language: navigator.language,
        platform: navigator.platform,
        hasAsync: hasAsync(),
        abortController: !!window.AbortController,
        webWorker: !!window.Worker,
        webAssembly: typeof WebAssembly === "object",
    }

    bf.isOutdatedBrowser = !(bf.hasAsync &&
        bf.abortController &&
        bf.webWorker &&
        bf.webAssembly)

    return bf
}
