import React, { Component } from 'react'
import PropTypes from 'prop-types'
import ReactJson from 'react-json-view'

import './TimeMachinePage.css'

// App component - represents the whole app
export default class TimeMachinePage extends Component {
  render() {

    const { state, action, repo, showRepo } = this.props;

    return (
      <div>
        <header>
          <h2>State Inspector</h2>
        </header>
        <div className="TimeMachinePage-body">
        {showRepo
        ?[
          <h4 key={1}>Repository</h4>,
          <ReactJson key={2} name={false} src={repo.toJS()} />]
        :[
        <h4 key={1}>Action</h4>,
        <ReactJson key={2} name={false} src={action} />,
        <h4 key={3}>State</h4>,
        <ReactJson key={4} name={false} src={state.toJS()} />]
        }
        </div>
      </div>
    )
  }
}

TimeMachinePage.propTypes = {
  state: PropTypes.object,
  prevState: PropTypes.object,
  action: PropTypes.object,
}
