// @flow

import type { Hand7 } from '../calc/hand'
import type { CombosItem } from '../calc/handCombos'
import type { HandEvalRequest, HandEvalResult } from '../calc/handEvaluator'

import { JsApi } from "./jsApi"
import { RestApi } from "./restApi"
import { WasmApi } from "./wasmApi"

export type ApiType = "localjs" | "js" | "gojs" | "wasm" | "rest"
export type CalcType = "basic" | "fast" | "loki"
export type RespType = "err" | "handCalc" | "handEval" | "handEvalPlayers"

export type ErrResp = {
	type: "err",
	err: string,
}

export type HandCalcResp = {
	type: "handCalc",
	err:          string,
	handStr:      string,
	hand:         Hand7,
	handValid:    boolean,
	handErr:      string,
	handRank:     number,
	hole:         string,
	handCat:      string,
	handCategory: string,
	handResult:   string,
	betterCombos: number,
	equalCombos:  number,
	worseCombos:  number,
	handCombos:   Array<CombosItem>,
}

export type HandEvalResp = {
	type: "handEval",
	request: HandEvalRequest,
	err: string,
	finalPlayers: number,

	games: number,
	wins: number,
	ties: number,
	pties: number, // pot ties, number of ties expressed as number of pots
	losses: number,

	// win probablity: a number between 0 and 1
	// the probability of winning the pot, includes the proportional ties
	wp: number,

	// winning odds (loss / win ratio)
	// represented as wo : 1
	// if wp is 1 then wo is 0
	// if wp is 0 then wo is Infinity
	wo: number,

	// expected return: the average profit or loss, is case you decide to bet
	// the value of the bet is subtracted
	// the ev is a number between (-1) and (number of players - 1)
	er: number,
}

export type HandEvalPlayersResp = {
	type: "handEvalPlayers",
	err: string,
	handEvals: Array<HandEvalResp>,
}

export type ApiOptions = {
	apiType: "localjs"|"js"|"gojs"|"wasm",
} | {
	apiType: "rest",
	server: string, 
	threads: number,
}

export interface PoserApiInterface {
	HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp | ErrResp>;
	HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp | ErrResp>;
	HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp | ErrResp>;
}

export class PoserApi implements PoserApiInterface {
	
	localjsApi: PoserApiInterface
	jsApi: PoserApiInterface
	gojsApi: PoserApiInterface
	restApi: PoserApiInterface
	wasmApi: PoserApiInterface

	constructor(secret: string) {
		this.jsApi = new JsApi()
		this.restApi = new RestApi(secret)
		this.wasmApi = new WasmApi()
	}

	getApiInterface(apiOptions: ApiOptions): PoserApiInterface {
		let apiInterface: PoserApiInterface
		switch(apiOptions.apiType) {
			case "js":
				apiInterface = this.jsApi
				break
			case "wasm":
				apiInterface = this.wasmApi
				break
			case "rest":
				apiInterface = this.restApi
				break
			default:
				throw new Error("invalid apiType: "+apiOptions.apiType)
		}

		return apiInterface
	}

	async HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp | ErrResp> {
		try {
			const apiInterface: PoserApiInterface = this.getApiInterface(apiOptions)
			return await apiInterface.HandCalc(apiOptions, handStr, calcType)
		} catch(ex) {
			return MakeErrResp(ex.toString())
		}
	}
	
	async HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp | ErrResp> {
		try {
			const apiInterface: PoserApiInterface = this.getApiInterface(apiOptions)
			return await apiInterface.HandEval(apiOptions, nsims, players, handStr, calcType)
		} catch(ex) {
			return MakeErrResp(ex.toString())
		}
	}

	async HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp | ErrResp> {
		try {
			const apiInterface: PoserApiInterface = this.getApiInterface(apiOptions)
			return await apiInterface.HandEvalPlayers(apiOptions, nsims, handStr, calcType)
		} catch(ex) {
			return MakeErrResp(ex.toString())
		}
	}
}

export function MakeErrResp(err: string): ErrResp {
	if(err.startsWith("Error: ")) {
		err = err.substring(7)
	}
	return {
		type: "err",
		err,
	}
}

export function MakeHandEvalResp(result: HandEvalResult): HandEvalResp {
	const resp: HandEvalResp = {
		type: "handEval",
		request: result.request,
		err: result.err,
		finalPlayers: result.finalPlayers ? result.finalPlayers : result.request.players,
	
		games: result.games,
		wins: result.wins,
		ties: result.ties,
		pties: result.pties, // pot ties, number of ties expressed as number of pots
		losses: result.losses,
		wp: 0,
		wo: 0,
		er: 0,
	}

	RecalcHandEvalResp(resp)

	return resp
}

export function  AddHandEvalResp(resp: HandEvalResp, result: HandEvalResp): void {
	if(!resp.request) {
		resp.request = result.request
	}

	if(resp.err === "") {
		resp.err = result.err
	}

	resp.games += result.games
	resp.wins += result.wins
	resp.ties += result.ties
	resp.pties += result.pties // pot ties, number of ties expressed as number of pots
	resp.losses += result.losses
}

export function RecalcHandEvalResp(resp: HandEvalResp): void {
	// calc pp, er (expected return), po, 
	resp.wp = (resp.wins + resp.pties) / resp.games
	resp.wo = resp.wp === 0 ? Infinity : 1 / resp.wp - 1
	const bet = 1
	const pot = bet * resp.finalPlayers
	resp.er = resp.wp * pot - bet
}
