import { connect } from 'react-redux'
import TimeMachineControls from './TimeMachineControls'

const mapStateToProps = (repo, ownProps) => {
  const currentBranch = repo.get("currentBranch")
  const branches = repo.get("branches")
  const branch = branches.get(currentBranch);
  const live = branch.get("live")
  const showRepo = repo.getIn(["options", "showRepo"]);
  const currentIndex = branch.get("currentIndex")
  const states = branch.get("states")
  const maxIndex = states.size-1
  return {
    branches: branches.keySeq(),
    currentBranch,
    currentIndex,
    maxIndex,
    live,
    showRepo,
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const e = ownProps.engine
  const af = e.actionFactory()
  
  return {
    goForward: (steps) => {
      const action = af.goForward(steps)
      dispatch(action)
    },
    goBack: (steps) => {
      const action = af.goBack(steps)
      dispatch(action)
    },
    goLive: () => {
      const action = af.goLive()
      dispatch(action)
    },
    doShowRepo: (showRepo) => {
      const action = af.setOption("showRepo", showRepo)
      dispatch(action)
    },
    switchBranch: (name) => {
      const action = af.switchBranch(name)
      dispatch(action)
    },
    saveBranch: (name) => {
      const action = af.saveBranch(name)
      dispatch(action)
    },
    resetBranch: (name) => {
      const action = af.resetBranch(name)
      dispatch(action)
    },
    removeBranch: (name) => {
      if(name === "master") {
        return
      }
      const action1 = af.switchBranch("master")
      dispatch(action1)
      const action2 = af.removeBranch(name)
      dispatch(action2)
    }
  }
}

const TimeMachineControlsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(TimeMachineControls)

export default TimeMachineControlsContainer
