import { connect } from 'react-redux'

import { engine } from '../../engine'

import { ApiType } from './components/ApiType'
import { CalcType } from './components/CalcType'

const e = engine.subEngine('home.settings')
const af = e.actionFactory();

const mapStateToProps = (state, repoHead, ownProps) => ({
  apiType: state.get("apiType"),
  apiTypeLabel: ApiType[state.get("apiType")].label,
  apiServer: state.get("apiServer"),
  jwtSecret: state.get("jwtSecret"),
  calcType: state.get("calcType"),
  calcTypeLabel: CalcType[state.get("calcType")].label,
  nSims: state.get("nSims"),
  workers: state.get("workers"),
  threads: state.get("threads"),
  players: state.get("players"),
  catsChart: state.get("catsChart"),
  catsTable: state.get("catsTable"),
  playersChart: state.get("playersChart"),
  playersTable: state.get("playersTable"),
  combosTable: state.get("combosTable"),
  denseKeyboard: state.get("denseKeyboard"),
  playersButtons: state.get("playersButtons"),
  devMode: state.get("devMode"),
  latency: state.get("latency"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), e.head(repo), ownProps)
 
const mapDispatchToProps = (dispatch, ownProps) => ({
  setApiType: (apiType) => {
    dispatch(af.value("apiType", apiType))
  },
  setApiServer: (apiServer) => {
    dispatch(af.value("apiServer", apiServer))
  },
  setJwtSecret: (jwtSecret) => {
    dispatch(af.value("jwtSecret", jwtSecret))
  },
  setCalcType: (calcType) => {
    dispatch(af.value("calcType", calcType))
  },
  setSims: (nSims) => {
    dispatch(af.value("nSims", nSims))
  },
  setWorkers: (workers) => {
    dispatch(af.value("workers", workers))
  },
  setThreads: (threads) => {
    dispatch(af.value("threads", threads))
  },
  setCatsChart: (checked) => {
    dispatch(af.value("catsChart", checked))
  },
  setCatsTable: (checked) => {
    dispatch(af.value("catsTable", checked))
  },
  setPlayersChart: (checked) => {
    dispatch(af.value("playersChart", checked))
  },
  setPlayersTable: (checked) => {
    dispatch(af.value("playersTable", checked))
  },
  setCombosTable: (checked) => {
    dispatch(af.value("combosTable", checked))
  },
  setDenseKeyboard: (checked) => {
    dispatch(af.value("denseKeyboard", checked))
  },
  setPlayersButtons: (checked) => {
    dispatch(af.value("playersButtons", checked))
  },
  setDevMode: (checked) => {
    dispatch(af.value("devMode", checked))
  },
  setLatency: (checked) => {
    dispatch(af.value("latency", checked))
  },
})

const SettingsContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default SettingsContainer
