import React from 'react'

import Card from "./Card"
import Category from "./Category"

import '../Table/Table.css'

const CombosTable = (props) => {
  const handResult = props.handResult
  const empty = handResult.isEmpty()

  if(empty) {
    return null
  }

  const handValid = handResult.get("handValid")

  if(!handValid) {
    return null
  }

  const handRank = handResult.get("handRank")
  const allCombos = handResult.get("betterCombos")+handResult.get("equalCombos")+handResult.get("worseCombos")
  const handCombos = props.handCombos

  let i = 0
  let rank = 0

  const style = {fontSize: 18, padding: 0}

  return (<div>
    <h3>
      Combos
    </h3>
    <table className="minimalistBlack">
      <thead>
        <tr>
          <th className="center">N</th>
          <th className="center">Hole</th>
          <th className="center">&times;</th>
          <th className="center">Cat</th>
          <th className="center">Cards</th>
          <th className="center">%</th>
        </tr>
      </thead>
    <tbody>
    {handCombos.map((comboItem)=>{
      const prc = 100 * (allCombos - rank) / allCombos
      const cnt = comboItem.get("count")
      const evalHandRank = comboItem.get("handRank")
      rank += cnt
      
      return <tr style={handRank === evalHandRank?{backgroundColor: "yellow"}:{}} key={i++}>
        <td className="right">
        {i}
        </td>
        <td className="center">
        <Card style={{...style, background: "transparent", margin: 0, height: 24}} html={true}>{comboItem.get("hole").substr(0, 2)}</Card>
        <Card style={{...style, background: "transparent", margin: 0, height: 24}} html={true}>{comboItem.get("hole").substr(2, 2)}</Card>
        &nbsp;
        </td>
        <td className="right">
          {/* &times; */}
          {cnt > 0 ? cnt : null}
        </td>
        <td className="center">
        <Category cat={comboItem.get("cat")} category={comboItem.get("category")}>{comboItem.get("category")}</Category>
        </td>
        <td className="center">
        <Category cat={comboItem.get("cat")} category={comboItem.get("category")}>{comboItem.get("result")}</Category>
        </td>
        <td className="right">
        {parseFloat(prc).toFixed(2)}
        </td>
      </tr>
    })}
    </tbody>
    </table>
    </div>
    )
}

export default CombosTable
