import React, { Component } from 'react'
import { Base64 } from 'js-base64'

export default class JwtSecretSetting extends Component {

  constructor(props) {
    super(props)
    this.state = {showSecret: false}
  }


  changeJwtSecret = event => {
    const base64Secret = Base64.encode(event.target.value)
    this.props.setJwtSecret(base64Secret)
  }

  changeShowSecret = event => {
    const newState = {showSecret: !this.state.showSecret}
    this.setState(newState)
  }

  render() {
    const type = this.state.showSecret ? "text" : "password"
    const secret = Base64.decode(this.props.jwtSecret)
    return (
      <span style={{width: 150}}>
        <input 
          style={{height: 30, width: 120, textAlign: "left"}} 
          type={type}
          name="JwtSecret"
          title="REST Server URL"
          value={secret}
          maxLength={64}
          onChange={this.changeJwtSecret.bind(this)}
          placeholder=""
        />
      <input type="checkbox" name="show" title="Show Secret" checked={this.state.showSecret} onChange={this.changeShowSecret.bind(this)}/>
      </span>
    )
  }
}
