import React from 'react'
import { BarChart, Bar, CartesianGrid, XAxis, YAxis, Tooltip, Legend } from 'recharts';
import { GetCatStr } from '../../calc/handRank'

import css from './EvalPlayersChart.module.css'

const EvalPlayersChart = (props) => {
    const handEval = props.handEval

    if(!handEval) {
      return null
    }
  
    const players = handEval.getIn(["request", "players"])
    const heroWpCats = handEval.get("heroWpCats")
    const heroLpCats = handEval.get("heroLpCats")
    const villainWpCats = handEval.get("villainWpCats")
    const villainLpCats = handEval.get("villainLpCats")

    if(!heroWpCats) {
      return null
    }

    const data = heroWpCats.map((v, i) => {
      return {
        cat: GetCatStr(i),
        hlp: -Math.floor(heroLpCats.get(i)*10000)/100,
        hwp: Math.floor(v*10000)/100,
        her: Math.floor(v*(players-2)*10000)/100,
        vlp: -Math.floor(villainLpCats.get(i)*10000)/100,
        vwp: Math.floor(villainWpCats.get(i)*10000)/100,
        ver: Math.floor(-villainLpCats.get(i)*(players-2)*10000)/100,
      }
    }
    ).toIndexedSeq().toArray()

    const style = {fontSize: 12}

    return (
    <BarChart style={style} className={css.chart}
      layout="horizontal"
      width={320}
      height={250}
      data={data}
      stackOffset="sign"
      margin={{ top: 5, right: 5, left: 5, bottom: 5 }}
    >
      <CartesianGrid stroke="#c0c0c0" strokeDasharray="3 3"/>
      <XAxis dataKey="cat" />
      <YAxis width={40} unit="%" yAxisId="left" />
      <Legend />
      <Tooltip />
      <Bar yAxisId="left" type="monotone" dataKey="hlp" stackId="h" fill="#339933"/>
      <Bar yAxisId="left" type="monotone" dataKey="hwp" stackId="h" fill="green" />
      <Bar yAxisId="left" type="monotone" dataKey="her" stackId="h" fill="lightgreen"/>
      <Bar yAxisId="left" type="monotone" dataKey="vlp" stackId="v" fill="#3333FF" />
      <Bar yAxisId="left" type="monotone" dataKey="vwp" stackId="v" fill="blue" />
      <Bar yAxisId="left" type="monotone" dataKey="ver" stackId="v" fill="lightblue" />
    </BarChart>
    )
  }

export default EvalPlayersChart
