import React, { Component } from 'react'

import { ApiType } from './ApiType'

import Select from 'react-select'

export default class ApiSetting extends Component {

  constructor(props) {
    super(props)

    this.options = []
    Object.keys(ApiType).map(key => this.options.push(ApiType[key]))
  }

  handleChange = selectedOption => {
    this.props.setApiType(selectedOption.value)
  }

  render() {
    const selectedOption = ApiType[this.props.apiType]
    return (
      <span style={{width: 150}}>
        <Select 
            value={selectedOption}
            onChange={this.handleChange}
            options={this.options}
        />
      </span>
    )
  }
}
