// @flow

const hsbSize: number = 1 << 16

const hsb = new Array<number>(hsbSize)

function init() {
	for(let i: number = 0; i < hsbSize; i++) {
		hsb[i] = getSlowHsb(i)
	}
}

function getSlowHsb(n: number): number {
	let i: number = -1

	while(n > 0) {
		i++
		n >>= 1
	}

	return i
}

// GetHsb16 function
// returns the highest significant bit in a 16-bit integer
// or -1 if no hsb
export function GetHsb16(n: number): number {
	return hsb[n]
}

init()
