import { connect } from 'react-redux'
import TimeMachinePage from './TimeMachinePage'

const mapStateToProps = (repo, ownProps) => {
  const e = ownProps.engine;
  const branch = e.currentBranchState(repo);
  const currentIndex = branch.get("currentIndex");
  const states = branch.get("states");
  const state = currentIndex < 0 ? undefined : states.get(currentIndex);
  const actions = branch.get("actions");
  const action = currentIndex < 0 ? undefined : actions.get(currentIndex);
  const prevState = currentIndex <= 0 ? undefined : states.get(currentIndex - 1);
  const showRepo = repo.getIn(['options', 'showRepo']);
  return {
    prevState: e.printableState(prevState),
    state: e.printableState(state),
    action,
    repo,
    showRepo,
  };
}

const mapDispatchToProps = (dispatch, ownProps) => ({
})

const TimeMachineContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(TimeMachinePage)

export default TimeMachineContainer
