// @flow

import type { CardRank } from './card'
import { GetCardRankStr } from './card'

/*
 * The handRank (hr) represents a value of the 7-card hand
 * It is a 24 bit integer number in the following format:
 *
 * hr[20:23] - hand category
 * hr[16:19] - card4 rank
 * hr[12:15] - card3 rank
 * hr[8:11] - card2 rank
 * hr[4:7] - card1 rank
 * hr[0:3] - card0 rank
 *
 * hand category -
 *  0 - high card
 *  1 - 1 pair
 *  2 - 2 pair
 *  3 - three of a kind
 *  4 - straight
 *  5 - flush
 *  6 - full house
 *  7 - 4 of a kind
 *  8 - straight flush
 *  F - bad hand rank
 *
 *  Special Value = 0 - means invalid hand
 */

export type HandRank = number
export type HandRankCat = number

export const HandRankCatHighCard: HandRankCat = (0)
export const HandRankCatOnePair: HandRankCat = (1)
export const HandRankCatTwoPair: HandRankCat = (2)
export const HandRankCatSet    : HandRankCat = (3)
export const HandRankCatStraight: HandRankCat = (4)
export const HandRankCatFlush  : HandRankCat = (5)
export const HandRankCatFullHouse: HandRankCat = (6)
export const HandRankCatQuads  : HandRankCat = (7)
export const HandRankCatStraightFlush: HandRankCat = (8)
export const HandRankCatMax    : HandRankCat = (9)
export const HandRankCatBad    : HandRankCat = (0xF)

export const HandRankNull: HandRank = (0x0FFFFF)
export const HandRankBad: HandRank = (0xFFFFFF)

const handRankMasks: Array<number> = [
	~0x0000F,
	~0x000F0,
	~0x00F00,
	~0x0F000,
	~0xF0000,
]

const handRankCatsStr: Array<string> = [
	"1",
	"2",
	"2+2",
	"3",
	"S",
	"F",
	"3+2",
	"4",
    "SF"
]

const handRankCategoriesStr = [
	"High",
	"1 Pair",
	"2 Pair",
	"Set",
	"Straight",
	"Flush",
	"Boat",
	"Quads",
    "StrFl"
]

const handRankCategoriesLongStr = [
	"High Card",
	"One Pair",
	"Two Pair",
	"Set",
	"Straight",
	"Flush",
	"Full House",
	"Quads",
    "Straight Flush"
]

export function MakeHandRank(): HandRank {
	return HandRankNull
}

export function SetCategory(handRank: HandRank, category: HandRankCat): HandRank {
	return (handRank & (~0xF<<20)) | (category << 20)
}

export function GetCategory(handRank: HandRank): HandRankCat {
	return (handRank >> 20) & 0xf
}

export function GetCategoryStr(category: HandRankCat): string {
	return handRankCategoriesStr[category]
}

export function GetCategoryLongStr(category: HandRankCat): string {
	return handRankCategoriesLongStr[category]
}

export function GetCatStr(category: HandRankCat): string {
	return handRankCatsStr[category]
}

export function GetHandCategoryStr(handRank: HandRank): string {
	return GetCategoryStr(GetCategory(handRank))
}

export function GetHandCatStr(handRank: HandRank): string {
	return GetCatStr(GetCategory(handRank))
}

export function SetCardRank(handRank: HandRank, n: number, cardRank: CardRank): HandRank {
	return (handRank & handRankMasks[n]) | (cardRank << (n << 2))
}

export function GetCardRank(handRank: HandRank, n: number): CardRank {
	return (handRank >> (n << 2)) & 0xf
}

export function HandRankStr(handRank: HandRank): string {
	return GetCardRankStr(GetCardRank(handRank, 4)) +
		GetCardRankStr(GetCardRank(handRank, 3)) +
		GetCardRankStr(GetCardRank(handRank, 2)) +
		GetCardRankStr(GetCardRank(handRank, 1)) +
		GetCardRankStr(GetCardRank(handRank, 0))
}
