// @flow

import type { Hand7 } from '../calc/hand'
import type { HandRank } from '../calc/handRank'
import type { CombosItem } from '../calc/handCombos'
import type { HandCalculator } from '../calc/handCalculator'
import type { HandEvalRequest, HandEvalResult } from '../calc/handEvaluator'
import type { CalcType, PoserApiInterface, ApiOptions, HandCalcResp, HandEvalResp, HandEvalPlayersResp } from "./poserApi"

import { MakeHandEvalResp } from "./poserApi"
import { StringToHand7, Hand7Bad, GetHand7HoleStr } from '../calc/hand'
import { GetHandCatStr, GetHandCategoryStr, HandRankStr } from '../calc/handRank'
import { HandRankBad } from '../calc/handRank'
import { Hand7Combos } from '../calc/handCombos'
import { HandEvaluator, MakeHandEvalRequest, MakeHandEvalResult, HandEvalRequestSetHand } from '../calc/handEvaluator'

import { BasicHandCalculator } from '../calc/basicHandCalculator'
import { LokiHandCalculator } from '../calc/lokiHandCalculator'

export class JsApi implements PoserApiInterface {

	handEvaluator: HandEvaluator

	constructor() {
		this.handEvaluator = new HandEvaluator()
	}

    MakeHandCalculator(calcType: CalcType): HandCalculator {
        let handCalculator: HandCalculator
    
        switch(calcType) {
            case "basic":
                handCalculator = new BasicHandCalculator()
                break
            case "fast":
                throw new Error("calculator type not supported in web browser: "+calcType)
            case "loki":
                handCalculator = new LokiHandCalculator()
                break
            default:
                throw new Error("invalid calculator type: "+calcType)
        }
        
        return handCalculator
    }
    
	async HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp> {
		const handRes: [Hand7, string] = StringToHand7(handStr)
		const hand = handRes[0]
		const err = handRes[1]

		let handErr: string = ""
		let handRank: HandRank = HandRankBad
		let holeStr: string = ""
		let handCat: string = ""
		let handCategory: string = ""
		let handResult: string = ""

		if(err) {
			handErr = err
		} else {
			const handCalc: HandCalculator = this.MakeHandCalculator(calcType)
			handRank = handCalc.CalcHandRank(hand)
			handCat = GetHandCatStr(handRank)
			handCategory = GetHandCategoryStr(handRank)
			handResult = HandRankStr(handRank)
			holeStr = GetHand7HoleStr(hand)
		}
		const handValid: boolean = hand !== Hand7Bad
		const combosResult: [Array<CombosItem>, number, number, number] = Hand7Combos(hand)
		const handCombos: Array<CombosItem> = combosResult[0]
		const betterCombos: number = combosResult[1]
		const equalCombos: number = combosResult[2]
		const worseCombos: number = combosResult[3]

		const result: HandCalcResp = {
			type: "handCalc",
			err:          err,
			handStr:      handStr,
			hand:         hand,
			handValid:    handValid,
			handErr:      handErr,
			handRank:     handRank,
			hole:         holeStr,
			handCat:      handCat,
			handCategory: handCategory,
			handResult:   handResult,
			handCombos:   handCombos,
			betterCombos: betterCombos,
			equalCombos:  equalCombos,
			worseCombos:  worseCombos,
		}

		return result
	}
	
	async HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp> {
        const handCalc: HandCalculator = this.MakeHandCalculator(calcType)
		const evalRequest: HandEvalRequest = MakeHandEvalRequest(nsims, players)

		let err = HandEvalRequestSetHand(evalRequest, handStr)

		const evalResult: HandEvalResult = MakeHandEvalResult(evalRequest, err)
		if(!err) {
			await this.handEvaluator.eval(handCalc, evalRequest, evalResult)
		}

		return MakeHandEvalResp(evalResult)
	}

	async HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp> {
        const handCalc: HandCalculator = this.MakeHandCalculator(calcType)
		const handEvals: Array<HandEvalResp> = []
		let err = ""

		for(let players: number = 2; players <= 9; players++) {
			const evalRequest: HandEvalRequest = MakeHandEvalRequest(nsims, players)
			err = HandEvalRequestSetHand(evalRequest, handStr)
			if(err) {
				break
			}
			const evalResult: HandEvalResult = MakeHandEvalResult(evalRequest, err)
			await this.handEvaluator.eval(handCalc, evalRequest, evalResult)
			handEvals.push(MakeHandEvalResp(evalResult))
			if(evalResult.err) {
				err = evalResult.err
				break
			}
		}

		const resp: HandEvalPlayersResp = {
			type: "handEvalPlayers",
			err: err,
			handEvals: handEvals,
		}

		return resp
	}
}
