import React, { Component } from 'react'

import { chopHand } from '../../utils/hand'

import { List } from 'immutable-sorted'

import Card from './Card'

export default class Hand extends Component {

  constructor(props) {
    super(props)
    this.hand0 = ""
    this.comp0 = null
  }

  render0() {
    const props = this.props
    const hand = props.hand || props.children
    const arr = props.handArr?props.handArr:List(chopHand(hand, 2))

    let n = 0
    for(let i = 0; i < arr.size; i++) {
      const code = arr.get(i)
      if(-1 <= code && code < 52) {
        // normal card
        n++
        if(n >= 2) {
          n = i+1
          break
        }
      }
    }

    let i = 0
    let comp = <div>
      {arr.size > 0 ? arr.slice(0, n).map(code => <Card key={i++} html={true} code={code}/>) : <Card key={i++} html={true} code={-1}/>}
      {arr.size <= 2 ? null : <>&nbsp;&nbsp;
      {arr.slice(n).map(code => <Card key={i++} html={true} code={code}/>)}</>}
    </div>
    return comp
  }

  render() {
    const props = this.props
    const hand = props.hand || props.children
    if(!this.hand0 || this.hand0 !== hand) {
      this.hand0 = hand
      this.comp0 = this.render0()
    }
    return this.comp0
  }
}
