import React, { Component } from 'react'

import { ThreadsOptions } from './ThreadsOptions'

import Select from 'react-select'

export default class ThreadsSetting extends Component {

  constructor(props) {
    super(props)

    this.options = []
    Object.keys(ThreadsOptions).map(key => this.options.push(ThreadsOptions[key]))
  }

  handleChange = selectedOption => {
    this.props.setThreads(selectedOption.value)
  }

  customStyles = {
    option: (provided, state) => ({
      ...provided,
      textAlign: "right",
    }),
    control: (provided) => ({
      ...provided,
    }),
    singleValue: (provided, state) => ({
      ...provided,
    }),
  }

  render() {
    const selectedOption = ThreadsOptions[this.props.threads.toString()]

    return (
      <span style={{width: 150}}>
        <Select
          styles={this.customStyles}
          value={selectedOption}
          onChange={this.handleChange}
          options={this.options}
        />
      </span>
    )
  }
}
