// @flow

import { PoserApi } from "./poserApi"

import type { CalcType, ErrResp, HandCalcResp, HandEvalResp, HandEvalPlayersResp } from './poserApi'
import type { PoserApiInterface, ApiOptions } from './poserApi'

import { MakeErrResp, AddHandEvalResp, RecalcHandEvalResp } from './poserApi'

export class PoserWorkersApi implements PoserApiInterface {

	workers: number
	poserApis: Array<PoserApiInterface>

	constructor(workers: number, secret: string) {
		this.workers = workers
		this.poserApis = new Array<PoserApiInterface>(workers)
		for(let i = 0; i < workers; i++) {
			this.poserApis[i] = new PoserApi(secret)
		}
	}

	async HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp | ErrResp> {
		return await this.poserApis[0].HandCalc(apiOptions, handStr, calcType)
	}
	
	async HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp | ErrResp> {
		let workers = this.workers

		const portion = Math.floor(nsims / workers)
		const remain = nsims - portion * workers
		const promises = Array<Promise<HandEvalResp | ErrResp>>(workers)
		for(let i = 0; i < workers; i++) {
			const n = portion + (i < remain ? 1 : 0)
			promises[i] = this.poserApis[i].HandEval(apiOptions, n, players, handStr, calcType)
		}

		let resp: HandEvalResp | ErrResp | null = null

		// Collect all the promises
		for(let i = 0; i < workers; i++) {
			try {
				const r: HandEvalResp | ErrResp = await promises[i]
				if(!resp) {
					resp = r
				} else if(!resp.err && r.err) {
					resp.err = r.err
				} else if(!resp.err && resp.type === "handEval" && r.type === "handEval") {
					AddHandEvalResp(resp, r)
				}
			} catch(ex) {
				if(!resp) {
					resp = MakeErrResp(ex.toString())
				} else if(!resp.err) {
					resp.err = ex.toString()
				}
			}
		}
		if(resp && resp.type === "handEval" && !resp.err) {
			RecalcHandEvalResp(resp)
		}
		
		return resp || MakeErrResp("no results collected")
	}

	getHandStr(handStr: string, players: number): string {
		let hStr = handStr

		// find out if there are any trailing _
		let index = hStr.length
		while(index > 0 && hStr.charAt(index-1) === '_') {
			index--
		}
		// index is now pointing at first character of the underscore trail
		let hStr1 = hStr.substring(0, index)
		const hStr2 = hStr.substring(index)

		// cut off the trailing player in hStr1
		if(hStr1.endsWith("p")) {
			hStr1 = hStr1.substr(0, hStr1.length - 2)
		}

		// now figure out the players specified in the middle
		const pi = hStr1.lastIndexOf("p")
		if(pi <= 0) {
			// no players specified something's wrong
			return ""
		}
		const n = hStr1.charCodeAt(pi - 1) - 48
		if(n < players) {
			// to many players for this hand
			return ""
		}

		const suffix = String.fromCharCode(48+players)+"p"
		return hStr1 + suffix + hStr2
	}

	async HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp | ErrResp> {
		const handEvals: Array<HandEvalResp> = []
		let err = ""

		const promises = Array<Promise<HandEvalResp | ErrResp>>(0)

		let hStr = handStr.trim().toLowerCase()
		if(hStr.charAt(1) !== 'p') {
			// doesn't start with players specification, let's apply the original approach
			for(let players: number = 2; players <= 9; players++) {
				promises[players - 2] = this.HandEval(apiOptions, nsims, players, hStr, calcType)
			}
		} else {
			for(let players: number = 2; players <= 9; players++) {
				const hStrN = this.getHandStr(hStr, players)
				if(hStrN) {
					promises[players - 2] = this.HandEval(apiOptions, nsims, players, hStrN, calcType)
				}
			}
		}

		for(let i = 0; i < promises.length; i++) {
			const resp: HandEvalResp = await promises[i]
			handEvals.push(resp)
			if(resp.err) {
				err = resp.err
			}
		}

		const resp: HandEvalPlayersResp = {
			type: "handEvalPlayers",
			err: err,
			handEvals: handEvals,
		}

		return resp
	}
}
