import React from 'react'

import Category from "./Category"
import EvalCatsChart from '../components/EvalCatsChart'
import { GetCatStr, GetCategoryStr } from '../../calc/handRank'

import '../Table/Table.css'
import { List } from 'immutable-sorted';

const catFmt = (handEval, field, cat) => {
  const pf = handEval.get(field)
  if(!pf) {
    return ""
  }
  let p = pf.get(cat)
  if(!p) {
    p = pf.get(cat.toString())
  }
  if(!p) {
    return ""
  }
  return (p*100).toFixed(2)+"%"
}

const catTotal = (handEval, field) => {
  const p = handEval.get(field)
  if(!p) {
    return ""
  }
  let total = 0
  for(let i = 0; i < p.size; i++) {
    total += p.get(i)
  }
  return (total*100).toFixed(2)+"%"
}

const EvalCatsTable = (props) => {
  const handResult = props.handResult
  const settings = props.settings
  const empty = handResult.isEmpty()

  if(empty) {
    return null
  }

  const handEval = props.handEval

  if(!handEval) {
    return null
  }

  let i = 1

  return (<div>
    {settings.get("catsChart") || settings.get("catsTable")?<h3>
        Categories
    </h3>:null}
    {settings.get("catsChart")?
      <EvalCatsChart {...props}/>
    :null}

    {settings.get("catsTable")?
      <table className="minimalistBlack">
        <thead>
          <tr>
            <th colSpan={2} title="Hero" className="center">Hero</th>
            <th rowSpan={2} className="center">Cat</th>
            <th colSpan={2} title="Villain" className="center">Villain</th>
          </tr>
          <tr>
            <th title="Hero Wins" className="center">Win</th>
            <th title="Hero Losses" className="center">Loss</th>
            <th title="Villain Wins" className="center">Win</th>
            <th title="Villain Losses" className="center">Loss</th>
          </tr>
        </thead>
      <tbody>
      {List([0, 1, 2, 3, 4, 5, 6, 7, 8]).map((cat)=>{
        return <tr key={i++}>
          <td className="right">
            {catFmt(handEval, "heroWpCats", cat)}
          </td>
          <td className="right">
            {catFmt(handEval, "heroLpCats", cat)}
          </td>
          <td title="Category" className="center">
            <Category cat={GetCatStr(cat)}>{GetCategoryStr(cat)}</Category>
          </td>
          <td className="right">
            {catFmt(handEval, "villainWpCats", cat)}
          </td>
          <td className="right">
            {catFmt(handEval, "villainLpCats", cat)}
          </td>
        </tr>
      }).toIndexedSeq()}
      </tbody>
        <tfoot>
          <tr className="strong">
            <th title="Hero Wins" className="center">
              {catTotal(handEval, "heroWpCats")}
            </th>
            <th title="Hero Losses" className="center">
              {catTotal(handEval, "heroLpCats")}
            </th>
            <th className="center">Total</th>
            <th title="Villain Wins" className="center">
              {catTotal(handEval, "villainWpCats")}
            </th>
            <th title="Villain Losses" className="center">
              {catTotal(handEval, "villainLpCats")}
            </th>
          </tr>
        </tfoot>
      </table>
    :null}
    </div>
  )
}

export default EvalCatsTable
