// @flow

import type { Rng } from './rng'

export class Sfc32 implements Rng
{
    a: number
    b: number
    c: number
    d: number

    constructor() {
        const now: number = Date.now()
        const a: number = now+Math.floor(Math.random()*1000000000)
        const b: number = 13*a+now+Math.floor(Math.random()*1000000000)
        const c: number = 17*b+now+Math.floor(Math.random()*1000000000)
        const d: number = 31*c+now+Math.floor(Math.random()*1000000000)
        this.seed(a, b, c, d)
    }

    seed(a: number, b: number, c: number, d: number): void {
        this.a = a
        this.b = b
        this.c = c
        this.d = d
    }

    next(): number {
        this.a >>>= 0
        this.b >>>= 0
        this.c >>>= 0
        this.d >>>= 0

        let t = (this.a + this.b) | 0
        this.a = this.b ^ (this.b >>> 9)
        this.b = this.c + (this.c << 3) | 0
        this.c = (this.c << 21) | (this.c >>> 11)
        this.d = this.d + 1 | 0
        t = t + this.d | 0
        this.c = this.c + t | 0
        return (t >>> 0)
    }
}
