import type { Schema } from 'duxen'

import SettingsSchema from '../Settings/SettingsSchema'

import { chopHand, isValidHand, getValidCardsCnt } from '../../utils/hand'

import { Map, List } from 'immutable-sorted'

// const cmp=(a,b)=>(a>b?1:(a<b?-1:0))
const schema:Schema = {

  settings: {
    type: 'schema',
    schema: SettingsSchema,
  },

  tanking: {
    type: 'value',
    persistent: false,
    initValue: false,
  },

  tankingMs: {
    type: 'value',
    persistent: false,
    initValue: 0,
  },

  hand: {
      type: 'value',
      persistent: true,
      initValue: "______________",
  },

  handEval: {
      type: 'value',
      persistent: false,
      initValue: Map(),
  },

  handEvalPlayers: {
    type: 'value',
    persistent: false,
    initValue: Map(),
  },

  handResult: {
    type: 'value',
    persistent: false,
    initValue: Map(),
  },

  handCombos: {
    type: 'value',
    persistent: false,
    initValue: Map(),
  },

  handArr: {
    type:'formula',
    props: ['hand'],
    recipe: (props) => List(chopHand(props.hand, 7)),
  },

  handValid: {
    type:'formula',
    props: ['handArr'],
    recipe: (props) => isValidHand(props.handArr),
  },

  handCardsCnt: {
    type:'formula',
    props: ['handArr'],
    recipe: (props) => getValidCardsCnt(props.handArr),
  },

  err: {
    type: 'value',
    persistent: false,
    initValue: "",
  },

  currentSuit: {
    type: 'value',
    persistent: true,
    initValue: "s",
  },

}

export default schema
