export default function getBrowserName() {
    const names = ["Edge", "Firefox", "Safari", "Chrome", "Opera"]
    const ua = navigator.userAgent

    for(let n = names.length - 1; n >= 0; n--) {
        const name = names[n]
        const i = ua.indexOf(name)
        if(i >= 0) {
            let j = i + name.length
            let k = j
            if(ua[j] === '/') {
                j++
                k = ua.indexOf(' ', j)
                if(k < 0) {
                    k = ua.length
                }
            }
            return name + " " + ua.substring(j, k)
        }
    }

    return "Unknown"
}
