// @flow

/*
 * Card is an integer in the following format:
 *
 * 876543210
 *    rrrrss
 *
 * rrrr - is a rank, a number between 0 and 12
 * ss - is a suit, a number between 0 and 3
 *
 */

export type Card = number
export type CardRank = number
export type CardSuit = number

export const CardNull: Card = 0x3D
export const CardWildCard: Card = 0x3B
export const CardBad: Card  = 0x3F
export const CardSize = 52

export const Card2Players     = (69)
export const Card3Players     = (70)
export const Card4Players     = (71)
export const Card5Players     = (72)
export const Card6Players     = (73)
export const Card7Players     = (74)
export const Card8Players     = (75)
export const Card9Players     = (76)

export const CardSuitClubs: CardSuit = (0)
export const CardSuitDiamonds: CardSuit = (1)
export const CardSuitHearts: CardSuit = (2)
export const CardSuitSpades: CardSuit = (3)
export const CardSuitSize: CardSuit = (4)
export const CardSuitPlayers: CardRank = (0xD)
export const CardSuitWild: CardRank = (0xE)
export const CardSuitBad: CardSuit = (0xF)

export const CardRank2: CardRank = (0)
export const CardRank3: CardRank = (1)
export const CardRank4: CardRank = (2)
export const CardRank5: CardRank = (3)
export const CardRank6: CardRank = (4)
export const CardRank7: CardRank = (5)
export const CardRank8: CardRank = (6)
export const CardRank9: CardRank = (7)
export const CardRank10: CardRank = (8)
export const CardRankJ: CardRank = (9)
export const CardRankQ: CardRank = (10)
export const CardRankK: CardRank = (11)
export const CardRankA: CardRank = (12)
export const CardRankSize: CardRank = (13)
export const CardRankWild: CardRank = (0xE)
export const CardRankBad: CardRank = (0xF)

const cardRankStr: Array<string> = [
	"2",
	"3",
	"4",
	"5",
	"6",
	"7",
	"8",
	"9",
	"T",
	"J",
	"Q",
	"K",
    "A",
]

const cardSuitStr: Array<string> = [
	"c",
	"d",
	"h",
	"s",
]

export function MakeCard(rank: CardRank, suit: CardSuit): Card {
	if(0 <= rank && rank < CardRankSize && 0 <= suit && suit < CardSuitSize) {
		return (rank << 2) | suit
	} else if(2 <= rank && rank < 9 && suit === CardSuitPlayers) {
		return Card2Players + (rank -2)
	} else if(rank === CardRankWild) {
		return CardWildCard
	}
	return CardBad
}

export function MakeCardStr(s: string): Card {
	const c1: string  = s.substr(0, 1)
	const c2: string = s.substr(1, 1)

	let rank: CardRank
	let suit: CardSuit

	switch(c1) {
	case '2':
		rank = 0
		break
	case '3':
		rank = 1
		break
	case '4':
		rank = 2
		break
	case '5':
		rank = 3
		break
	case '6':
		rank = 4
		break
	case '7':
		rank = 5
		break
	case '8':
		rank = 6
		break
	case '9':
		rank = 7
		break
	case 't':
	case 'T':
		rank = 8
		break
	case 'j':
	case 'J':
		rank = 9
		break
	case 'q':
	case 'Q':
		rank = 10
		break
	case 'k':
	case 'K':
		rank = 11
		break
	case 'a':
	case 'A':
		rank = 12
		break
	case '*':
	case '_':
		rank = CardRankWild
		break
	case '?':
	default:
		rank = CardRankBad
		break
	}

	switch(c2) {
	case 'c':
	case 'C':
		suit = 0
		break
	case 'd':
	case 'D':
		suit = 1
		break
	case 'h':
	case 'H':
		suit = 2
		break
	case 's':
	case 'S':
		suit = 3
		break
	case 'p':
	case 'P':
		suit = CardSuitPlayers
		break
		case '*':
	case '_':
		suit = CardSuitWild
		break
	case '?':
	default:
		suit = CardSuitBad
		break
	}

	return MakeCard(rank, suit)
}

export function GetRank(card: Card): CardRank {
	return card >> 2
}

export function GetSuit(card: Card): CardSuit {
	return card & 3
}

export function GetRankStr(card: Card): string {
	return cardRankStr[card >> 2]
}

export function GetSuitStr(card: Card): string {
	return cardSuitStr[card & 3]
}

export function GetCardStr(card: Card): string {
	return GetRankStr(card) + GetSuitStr(card)
}

export function GetCardRankStr(cardRank: CardRank): string {
	return cardRankStr[cardRank]
}

export function GetCardSuitStr(cardSuit: CardSuit): string {
	return cardSuitStr[cardSuit]
}
