import React from 'react'

import Category from "./Category"
import EvalPlayersChart from '../components/EvalPlayersChart'

import '../Table/Table.css'

const EvalPlayersTable = (props) => {
    const handResult = props.handResult
    const settings = props.settings
    const empty = handResult.isEmpty()
  
    if(empty) {
      return null
    }
  
    const handValid = handResult.get("handValid")
  
    if(!handValid) {
      return null
    }
  
    if(props.handEvalPlayers.isEmpty()) {
        return null
    }

    let i = 1

    return (<div>
    {settings.get("playersChart") || settings.get("playersTable")?<h3>
        Players
    </h3>:null}
    {settings.get("playersChart")?
      <EvalPlayersChart {...props}/>
    :null}

    {settings.get("playersTable")?
      <table className="minimalistBlack">
        <thead>
          <tr>
            <th className="center">Players</th>
            <th title="Return value (%)" className="center">ER</th>
            <th title="Win probabilty (%)" className="center">WP</th>
            <th title="Win odds (loss : win) presented as n : 1" className="center">WO</th>
          </tr>
        </thead>
      <tbody>
      {props.handEvalPlayers.map((handEval)=>{
        const er = handEval.get("er")
        const wp = handEval.get("wp")
        const wo = handEval.get("wo")
        const cat = er >= 0.25 ? "3" : er <= -0.25 ? "F" : er >= 0 ? "1" : "2"
            
        const style = handEval.get("finalPlayers") === props.handEval.get("finalPlayers")?{backgroundColor: "yellow"}:{}
        return <tr style={style} key={i++}>
          <td className="center">
          {handEval.get("finalPlayers")}
          </td>
          <td title="Expected Return (%)" className="right">
          <Category cat={cat}>{parseFloat(er*100).toFixed(0)}%</Category>
          </td>
          <td title="Win probabilty (%)" className="right">
          <Category cat={cat}>{parseFloat(wp*100).toFixed(0)}%</Category>
          </td>
          <td title="Win odds (loss : win) presented as n : 1" className="right">
          <Category cat={cat}>{wo > 1000 ? ">100" : wo > 10 ? parseFloat(wo).toFixed(0) : parseFloat(wo).toFixed(2)}:1</Category>
          </td>
        </tr>
      }).toIndexedSeq()}
      </tbody>
      </table>
    :null}
     </div>
    )
  }

export default EvalPlayersTable
