import { engine } from '.'

export const loadState = (appId) => {
  try {
    const serializedState = localStorage.getItem(appId);
    if (serializedState === null) {
      return undefined;
    }
    const parsedState = JSON.parse(serializedState);
    return parsedState;
  } catch (err) {
    return undefined;
  }
};

let lastAppId;
let lastState;
let newAppId;
let newState;

export const saveState = (appId, state) => {
    newAppId = appId;
    newState = state;
};

const persistState = () => {
  try {
    if(lastAppId === newAppId && lastState === newState) {
      return;
    }
    const liveState = engine.liveState(newState);
    const persistableState = engine.persistableState(liveState);
    const serializedState = JSON.stringify(persistableState);
    localStorage.setItem(newAppId, serializedState);
    lastAppId = newAppId;
    lastState = newState;
  } catch (err) {
    console.log("DUXEN ERROR: persistState: ", err);
  }
};

setInterval(persistState, 1000);
