// @flow

import type { Card } from './card'
import type { Hand7 } from './hand'
import type { HandRank } from './handRank'
import type { HandCalculator } from './handCalculator'

import { CardSize } from './card'
import { GetHand7Card, SetHand7Card, GetHand7HoleStr, Hand7Bad } from './hand'
import { GetHandCatStr, GetHandCategoryStr, HandRankStr } from './handRank'
import { BasicHandCalculator } from './basicHandCalculator'

import { SortedMap } from 'immutable-sorted'

export type CombosItem = {
	hole:     string,
	handRank: number,
	cat:      string,
	category: string,
	result:   string,
	count:    number,
}

export function Hand7Combos(hand: Hand7): [Array<CombosItem>, number, number, number] {
	if(hand === Hand7Bad) {
		return [[], 0, 0, 0]
	}
	const handCalc: HandCalculator = new BasicHandCalculator()
	const handRank: HandRank = handCalc.CalcHandRank(hand)
	let betterCombos: number = 0
	let equalCombos: number = 0
	let worseCombos: number = 0

	let evalMap: SortedMap<HandRank, CombosItem> = SortedMap()

	const handRes: CombosItem = {
		hole:     GetHand7HoleStr(hand),
		handRank: handRank,
		cat:      GetHandCatStr(handRank),
		category: GetHandCategoryStr(handRank),
		result:   HandRankStr(handRank),
		count:    0,
    }
    
	evalMap = evalMap.set(handRank, handRes)

	let mask0 = 0
	let mask1 = 0

	for(let i: number = 0; i < 7; i++) {
        const card: Card = GetHand7Card(hand, i)
        if(card < 32) {
            mask0 |= (1 << card)
        } else {
            mask1 |= (1 << (card - 32))
        }
	}

	let h: Hand7 = hand

	for(let c1: Card = 1; c1 < CardSize; c1++) {
        if(c1 < 32) {
            const c1bit: number = 1 << c1
            if((mask0 & c1bit) !== 0) {
                continue
            }
        } else {
            const c1bit: number = 1 << (c1 - 32)
            if((mask1 & c1bit) !== 0) {
                continue
            }
        }
		for(let c2: Card = 0; c2 < c1; c2++) {
            if(c2 < 32) {
                const c2bit: number = 1 << c2
                if((mask0 & c2bit) !== 0) {
                    continue
                }
            } else {
                const c2bit: number = 1 << (c2 - 32)
                if((mask1 & c2bit) !== 0) {
                    continue
                }
            }
			const card1: Card = c1
			const card2: Card = c2
			h = SetHand7Card(h, 0, card1)
			h = SetHand7Card(h, 1, card2)
			const hRank: HandRank = handCalc.CalcHandRank(h)

			let hRes: ?CombosItem = evalMap.get(hRank)
			if(hRes) {
				hRes.count++
			} else {
				hRes = {
					hole:     GetHand7HoleStr(h),
					handRank: hRank,
					cat:      GetHandCatStr(hRank),
					category: GetHandCategoryStr(hRank),
					result:   HandRankStr(hRank),
					count:    1,
				}
				evalMap = evalMap.set(hRank, hRes)
			}

			if(hRank > handRank) {
				betterCombos++
			} else if(hRank === handRank) {
				equalCombos++
			} else {
				worseCombos++
			}
		}
	}
	const hResArr = evalMap.toIndexedSeq().reverse().toArray()

	return [hResArr, betterCombos, equalCombos, worseCombos]
}
