import React from 'react'

import Card from './Card'

const SparseCardsEntry = (props) => {
  const suit = props.currentSuit
  const playersStyle = {...props.style, lineHeight: "0px", fontSize: 28}
  const playersButtons = props.settings.get("playersButtons")
  const x = (card) => playersButtons ? card : null

  return <div>
    <div>
    {x(<Card {...props} style={{...playersStyle}} card="9P"/>)}
    &nbsp;
    <Card {...props} style={suit === "s" ? {...props.style} : {...playersStyle, backgroundColor: "lightgrey"}} card={suit === "s" ? "As" : "Xs"}/>
    <Card {...props} style={suit === "h" ? {...props.style} : {...playersStyle, backgroundColor: "lightgrey"}} card={suit === "h" ? "Ah" : "Xh"}/>
    <Card {...props} style={suit === "d" ? {...props.style} : {...playersStyle, backgroundColor: "lightgrey"}} card={suit === "d" ? "Ad" : "Xd"}/>
    <Card {...props} style={suit === "c" ? {...props.style} : {...playersStyle, backgroundColor: "lightgrey"}} card={suit === "c" ? "Ac" : "Xc"}/>
    &nbsp;
    {x(<Card {...props} style={{...playersStyle}} card="5P"/>)}
    </div>
    <div>
    {x(<Card {...props} style={{...playersStyle}} card="8P"/>)}
    &nbsp;
    <Card {...props} card={"K"+suit}/>
    <Card {...props} card={"Q"+suit}/>
    <Card {...props} card={"J"+suit}/>
    <Card {...props} card={"T"+suit}/>
    &nbsp;
    {x(<Card {...props} style={{...playersStyle}} card="4P"/>)}
    </div>
    <div>
    {x(<Card {...props} style={{...playersStyle}} card="7P"/>)}
    &nbsp;
    <Card {...props} card={"9"+suit}/>
    <Card {...props} card={"8"+suit}/>
    <Card {...props} card={"7"+suit}/>
    <Card {...props} card={"6"+suit}/>
    &nbsp;
    {x(<Card {...props} style={{...playersStyle}} card="3P"/>)}
    </div>
    <div>
    {x(<Card {...props} style={{...playersStyle}} card="6P"/>)}
    &nbsp;
    <Card {...props} card={"5"+suit}/>
    <Card {...props} card={"4"+suit}/>
    <Card {...props} card={"3"+suit}/>
    <Card {...props} card={"2"+suit}/>
    &nbsp;
    {x(<Card {...props} style={{...playersStyle}} card="2P"/>)}
    </div>
  </div>
}

export default SparseCardsEntry
