// @flow

import type { Card } from './card'
import type { Hand7 } from './hand'
import type { HandRank } from './handRank'
import type { HandCalculator } from './handCalculator'

import { CardRankSize, CardSuitSize, CardRankA } from './card'
import { GetHand7Card } from './hand'
import { MakeHandRank, SetCategory, SetCardRank } from './handRank'
// import { HandRankCatHighCard, HandRankCatOnePair, HandRankCatTwoPair, HandRankCatSet, HandRankCatStraight, HandRankCatFlush, HandRankCatFullHouse, HandRankCatQuads, HandRankCatStraightFlush } from './handRank'
// import { GetHsb16 } from './hsb'

const POKER_HAND = 5

const  HIGH = 0
const  PAIR = 1
const  TWOPAIR = 2
const  THREEKIND = 3
const  STRAIGHT = 4
const  FLUSH = 5
const  FULLHOUSE = 6
const  FOURKIND = 7
const  STRAIGHTFLUSH = 8
const  FIVEKIND = 9

const  ID_GROUP_SIZE  = (CardRankSize*CardRankSize*CardRankSize*CardRankSize*CardRankSize)

export class LokiHandCalculator implements HandCalculator {
    CalcHandRank(hand: Hand7): HandRank {
        return calcHandRank(hand)
    }
}

function ID_ExistsStraightFlush(h: Array<number>, straight_high: Array<number>, major_suit: number): boolean {
    let i: number
    let straight: number
    let high: number
    const present: Array<boolean> = new Array<boolean>(CardRankSize)
    
    for (i=0;i<CardRankSize;i++) present[i]=false

    for (i=0;i<h.length;i++)
            if (getSuit(h[i]) === major_suit)
                    present[getRank(h[i])] = true

    straight = present[CardRankA] ? 1 : 0
    high = 0
    for (i=0;i<CardRankSize;i++) {
        if (present[i]) {
            if ( (++straight) >= POKER_HAND)
                high = i
        } else straight = 0
    }
    if (high === 0) return false
    straight_high[0] = high
    return true
}

/**********************************************************************/
// DENIS PAPP'S HAND RANK IDENTIFIER CODE:
/**********************************************************************/


// suit: CardSuitSize means any
// not_allowed: CardRankSize means any
// returns ident value
function ID_KickerValue(paired: Array<number>, kickers: number, not_allowed0: number, not_allowed1: number): number {
    let i: number = CardRankA
    let value: number = 0
    while (kickers !== 0) {
        while ( paired[i]===0 || i===not_allowed0 || i===not_allowed1)
            i--
        kickers--
        value+=pow(CardRankSize,kickers)*i
        i--
    }
    return value
}

function ID_KickerValueSuited(h: Array<number>, kickers: number, suit: number): number {
    let i: number
    let value: number=0

    const present: Array<boolean> = new Array<boolean>(CardRankSize)
    for (i=0;i<CardRankSize;i++) present[i] = false

    for (i=0;i<h.length;i++)
        if (getSuit(h[i]) === suit)
            present[getRank(h[i])] = true

    i = CardRankA
    while (kickers !== 0) {
        while (present[i] === false) i--
        kickers--
        value += pow(CardRankSize,kickers)*i
        i--
    }
    return value
}

function calcHandRank(h: Hand7): number
{
    const bh: Array<Card> = new Array<number>(7)
    
    for(let i = 0; i < 7; i++)
    {
        bh[i] = GetHand7Card(h, i)
    }
    let loki: number = rankHand_loki(bh)
    
    // Convert Loki value into HandRank
    const hc = Math.floor(loki / ID_GROUP_SIZE)
    loki %= ID_GROUP_SIZE
    let cr0 = loki % CardRankSize
    loki = Math.floor(loki / CardRankSize)
    let cr1 = loki % CardRankSize
    loki = Math.floor(loki / CardRankSize)
    let cr2 = loki % CardRankSize
    loki = Math.floor(loki / CardRankSize)
    let cr3 = loki % CardRankSize
    loki = Math.floor(loki / CardRankSize)
    let cr4 = loki % CardRankSize
    loki = Math.floor(loki / CardRankSize)
    
    // Fix the full house situation
    if(hc === FULLHOUSE)
    {
        cr4 = cr3 = cr2 = cr1
        cr1 = cr0
    }
    else if(hc === PAIR)
    {
        cr4 = cr3
    }
    else if(hc === THREEKIND)
    {
        cr4 = cr3 = cr2
    }
    else if(hc === FOURKIND)
    {
        cr4 = cr3 = cr2 = cr1
    }
    else if(hc === TWOPAIR)
    {
        cr4 = cr3 = cr2
        cr2 = cr1
    }
    else if(hc === STRAIGHT)
    {
        cr4 = cr0
        cr3 = cr4-1
        cr2 = cr3-1
        cr1 = cr2-1
        cr0 = cr1===0?12:cr1-1
    }
    else if(hc === STRAIGHTFLUSH)
    {
        cr4 = cr0
        cr3 = cr4-1
        cr2 = cr3-1
        cr1 = cr2-1
        cr0 = cr1===0?12:cr1-1
    }

    let hv: HandRank = MakeHandRank()
    hv = SetCategory(hv, hc)
    hv = SetCardRank(hv, 0, cr0)
    hv = SetCardRank(hv, 1, cr1)
    hv = SetCardRank(hv, 2, cr2)
    hv = SetCardRank(hv, 3, cr3)
    hv = SetCardRank(hv, 4, cr4)
    
    return hv
}

/**
 * Get a numerical ranking of this hand.
 * Uses java based code, so may be slower than using the native
 * methods, but is more compatible this way.
 *
 * Based on Denis Papp's Loki Hand ID code (id.cpp)
 * Given a 1-9 card hand, will return a unique rank 
     * such that any two hands will be ranked with the 
     * better hand having a higher rank. 
 *
 * @param h a 1-9 card hand
 * @return a unique number representing the hand strength of the best 
 * 5-card poker hand in the given 7 cards. The higher the number, the better
 * the hand is.
 */
function rankHand_loki(h: Array<number>): number
{
    let straight: boolean = false
    let flush: boolean = false
    const max_hand: number = (h.length >= POKER_HAND ? POKER_HAND : h.length)
    let r: number
    let c: number
    let rank: number
    let suit: number
    
    // pair data
    let group_size: Array<number> = new Array<number>(POKER_HAND+1)
    let paired: Array<number> = new Array<number>(CardRankSize)
    let pair_rank0: Array<number> = new Array<number>(POKER_HAND+1)
    let pair_rank1: Array<number> = new Array<number>(POKER_HAND+1)

    // straight
    let straight_high: number = 0
    let straight_size: number
    // flush
    let suit_size: Array<number> = new Array<number>(CardSuitSize)
    let major_suit: number = 0

    // determine pairs, dereference order data, check flush
    for (r=0;r<CardRankSize;r++) paired[r] = 0
    for (r=0;r<CardSuitSize;r++) suit_size[r] = 0
    for (r=0;r<=POKER_HAND;r++) group_size[r] = 0
    for (r=0;r<h.length;r++)
    {
        // int tmpint = r
        rank = getRank(h[r])
        suit = getSuit(h[r])
        
        paired[rank]++
        group_size[paired[rank]]++
        if (paired[rank] !== 0)
            group_size[paired[rank]-1]--
        if ((++suit_size[suit]) >= POKER_HAND)
        {
            flush = true
            major_suit = suit
        }
    }
    // Card.ACE low?
    straight_size = paired[CardRankA] !== 0 ? 1 : 0

    for (let i=0;i<(POKER_HAND+1);i++) {
        pair_rank0[i] = CardRankSize
        pair_rank1[i] = CardRankSize
    }

    // check for straight and pair data
    for (r=0;r<CardRankSize;r++) {
        // check straight
        if (paired[r]!==0) {
            if ( (++straight_size)>=POKER_HAND ) {
                straight = true
                straight_high =r
            }
        } else
            straight_size = 0

        // get pair ranks, keep two highest of each
        c = paired[r]
        if ( c !== 0 ) {
            pair_rank1[c] = pair_rank0[c]
            pair_rank0[c] = r
        }
    }

    // now id type
    let ident: number
    
    let str_hi: Array<number> = new Array<number>(1)
    str_hi[0] = (straight_high)
     
    if (group_size[POKER_HAND] !== 0) {
        ident = FIVEKIND*ID_GROUP_SIZE
        ident+=pair_rank0[POKER_HAND]
    } else if ( straight && flush && ID_ExistsStraightFlush(h,str_hi,major_suit)) {
        straight_high = str_hi[0]
        ident = STRAIGHTFLUSH*ID_GROUP_SIZE
        ident+=straight_high
    } else if (group_size[4] !== 0) {
        ident = FOURKIND*ID_GROUP_SIZE
        ident+=pair_rank0[4]*CardRankSize
        pair_rank1[4] = CardRankSize    // just in case 2 sets quads
        ident+=ID_KickerValue(paired,1,pair_rank0[4],pair_rank1[4])
    } else if (group_size[3]>=2) {
        ident = FULLHOUSE*ID_GROUP_SIZE
        ident+=pair_rank0[3]*CardRankSize
        ident+=pair_rank1[3]
    } else if (group_size[3]===1 && group_size[2]!==0) {
        ident = FULLHOUSE*ID_GROUP_SIZE
        ident+=pair_rank0[3]*CardRankSize
        ident+=pair_rank0[2]
    } else if (flush) {
        ident = FLUSH*ID_GROUP_SIZE
        ident+=ID_KickerValueSuited(h,5,major_suit)
    } else if (straight) {
        ident = STRAIGHT*ID_GROUP_SIZE
        ident+=straight_high
    } else if (group_size[3]===1) {
        ident = THREEKIND*ID_GROUP_SIZE
        ident+=pair_rank0[3]*CardRankSize*CardRankSize
        ident+=ID_KickerValue(paired,max_hand-3,pair_rank0[3],pair_rank1[3])
    } else if (group_size[2]>=2) {
        ident = TWOPAIR*ID_GROUP_SIZE
        ident+=pair_rank0[2]*CardRankSize*CardRankSize
        ident+=pair_rank1[2]*CardRankSize
        ident+=ID_KickerValue(paired,max_hand-4,pair_rank0[2],pair_rank1[2])
    } else if (group_size[2]===1) {
        ident = PAIR*ID_GROUP_SIZE
        ident+=pair_rank0[2]*CardRankSize*CardRankSize*CardRankSize
        ident+=ID_KickerValue(paired,max_hand-2,pair_rank0[2],pair_rank1[2])
    } else {
        ident = HIGH*ID_GROUP_SIZE
        ident+=ID_KickerValue(paired,max_hand,pair_rank0[2],pair_rank1[2])
    }
    return ident
}


function pow(n: number, p: number): number {
    let res: number = 1
    while (p-- > 0)
        res *= n
    return res
}

function getRank(x: number): number {
    return Math.floor(x/CardSuitSize)
}

function getSuit(x: number): number {
    return x%CardSuitSize
}
