import React, { Component } from 'react'
import ReactSwitch from 'react-switch'

import css from './CheckboxGroup.module.css'

export default class CheckboxGroup extends Component {

  catsChartChange(checked) {
    this.props.setCatsChart(checked)
  }

  catsTableChange(checked) {
    this.props.setCatsTable(checked)
  }

  playersChartChange(checked) {
    this.props.setPlayersChart(checked)
  }

  playersTableChange(checked) {
    this.props.setPlayersTable(checked)
  }

  combosTableChange(checked) {
    this.props.setCombosTable(checked)
  }

  denseKeyboardChange(checked) {
    this.props.setDenseKeyboard(checked)
  }

  playersButtonsChange(checked) {
    this.props.setPlayersButtons(checked)
  }
  
  devModeChange(checked) {
    this.props.setDevMode(checked)
  }

  latencyChange(checked) {
    this.props.setLatency(checked)
  }

  render() {
    const props = this.props
    const switchHeight = 20
    const switchWidth = 40

    return (
      <table>
        <tbody>
        <tr>
          <td className={css.right}>
            Players Chart:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.playersChartChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.playersChart}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Players Table:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.playersTableChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.playersTable}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Categories Chart:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.catsChartChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.catsChart}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Categories Table:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.catsTableChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.catsTable}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Combos Table:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.combosTableChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.combosTable}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Dense Keyboard:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.denseKeyboardChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.denseKeyboard}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Players Buttons:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.playersButtonsChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.playersButtons}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Dev Mode:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.devModeChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.devMode}/>
          </td>
        </tr>
        <tr>
          <td className={css.right}>
            Show Latency:
          </td>
          <td>
            <ReactSwitch className={css.switch} onChange={this.latencyChange.bind(this)} height={switchHeight} width={switchWidth} checked={props.latency}/>
          </td>
        </tr>
        </tbody>
      </table>
    )
  }
}
