import React from 'react'

import css from './Category.module.css'

const Cat2Color = (cat) => {
  let color
  switch(cat) {
    case "SF":
        color = "#ff4e4e" // red
        break
    case "4":
        color = "#cf9cff" // magenta
        break
    case "3+2":
        color = "#9c9cff" // blue
        break
    case "F":
        color = "#ff9c9c" // red
        break
    case "S":
        color = "#9cffff" // cyan
        break
    case "3":
        color = "#9cff9c" // green
        break
    case "2+2":
        color = "#ffcf9c" // orange
        break
    case "2":
        color = "#ffff9c" // yellow
        break
    case "1":
        color = "white"
        break
    case "error":
        color = "pink" // "#ff1010"
        break
    default:
      color = "grey"
      break
  }
  return color
}

const Cat2Cn = (cat) => {
  let cn
  switch(cat) {
    case "error":
      cn = css.err
      break
    default:
      cn = css.cat
      break
  }

  return cn
}

const Category = (props) => (
  <span title={props.category} style={{...props.style, backgroundColor: Cat2Color(props.cat)}} className={Cat2Cn(props.cat)}>
    &nbsp;
    {props.children}
    &nbsp;
  </span>
)

export default Category
