import React, { Component } from 'react'

import isMobileDevice from '../../utils/isMobileDevice'
import getBrowserFeatures from '../../utils/getBrowserFeatures'
import Category from "./Category"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import '../Table/Table.css'

export default class BrowserFeatures extends Component {
    isMobileDevice
    browserFeatures

    constructor(props) {
        super(props)
        this.isMobileDevice = isMobileDevice()
        this.browserFeatures = getBrowserFeatures()
    }

    render() {
        return (<div>
            {this.browserFeatures.isOutdatedBrowser ? 
            <div>
                <Category style={{height: 36}} cat="error">
                    <FontAwesomeIcon color="red" icon="exclamation-triangle"/>
                    &nbsp;
                    Missing Critical Features
                    &nbsp;
                    <FontAwesomeIcon color="red" icon="exclamation-triangle"/>
                    <br/>
                    Please Update Your Web Browser
                </Category>
                <br/>
                <br/>
            </div> : null}
            <table className="minimalistBlack">
                <thead>
                    <tr>
                        <th className="center">
                            Feature
                        </th>
                        <th className="center">
                            Value
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className="right">
                            name:
                        </td>
                        <td className="center">
                            {this.browserFeatures.name}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            language:
                        </td>
                        <td className="center">
                            {this.browserFeatures.language}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            platform:
                        </td>
                        <td className="center">
                            {this.browserFeatures.platform}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            mobile:
                        </td>
                        <td className="center">
                            {this.isMobileDevice?"Yes":"No"}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            async/await:
                        </td>
                        <td className="center">
                            {this.browserFeatures.hasAsync?<FontAwesomeIcon color="green" icon="check"/>:<FontAwesomeIcon color="red" icon="exclamation-triangle"/>}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            abort controller:
                        </td>
                        <td className="center">
                            {this.browserFeatures.abortController?<FontAwesomeIcon color="green" icon="check"/>:<FontAwesomeIcon color="red" icon="exclamation-triangle"/>}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            web worker:
                        </td>
                        <td className="center">
                            {this.browserFeatures.webWorker?<FontAwesomeIcon color="green" icon="check"/>:<FontAwesomeIcon color="red" icon="exclamation-triangle"/>}
                        </td>
                    </tr>
                    <tr>
                        <td className="right">
                            web assembly:
                        </td>
                        <td className="center">
                            {this.browserFeatures.webAssembly?<FontAwesomeIcon color="green" icon="check"/>:<FontAwesomeIcon color="red" icon="exclamation-triangle"/>}
                        </td>
                    </tr>
                </tbody>
            </table>

        </div>)
    }
}
