import React, { Component } from 'react'

import Category from '../../components/Category'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import css from './SettingsButtons.module.css'

export default class PlayersSetting extends Component {

  inc() {
    this.props.incPlayers()
  }
  
  dec() {
    this.props.decPlayers()
  }
  
  render() {
    const style = {fontSize:20, height: 32}

    return (
      <span>
        <Category style={style} cat="1">
        {this.props.children?<>{this.props.children}&nbsp;</>:null}
        {this.props.players}
        </Category>
        &nbsp;
        <button className={css.button} title="Decrease number of players" onClick={this.dec.bind(this)}><FontAwesomeIcon icon="user-minus"/></button>
        <button className={css.button} title="Increase number of players" onClick={this.inc.bind(this)}><FontAwesomeIcon icon="user-plus"/></button>
      </span>
    )
  }
}
