// @flow

import type { CardRank, CardSuit } from './card'
import type { Hand7 } from './hand'
import type { HandRank } from './handRank'
import type { HandCalculator } from './handCalculator'

import { CardRankSize, CardSuitSize, CardRank2, CardRankA } from './card'
import { GetHand7Rank, GetHand7Suit } from './hand'
import { MakeHandRank, SetCategory, SetCardRank } from './handRank'
import { HandRankCatHighCard, HandRankCatOnePair, HandRankCatTwoPair, HandRankCatSet, HandRankCatStraight, HandRankCatFlush, HandRankCatFullHouse, HandRankCatQuads, HandRankCatStraightFlush } from './handRank'
import { GetHsb16 } from './hsb'

export class BasicHandCalculator implements HandCalculator {
	rankCnts: Array<number> // [CardRankSize]
	suitCnts: Array<number> // [4]
    suitMasks: Array<number> // [4]

    constructor() {
        this.rankCnts = new Array<number>(CardRankSize)
        this.suitCnts = new Array<number>(CardSuitSize)
        this.suitMasks = new Array<number>(CardSuitSize)
    }

    CalcHandRank(hand: Hand7): HandRank {
        return this.calcHandRank(hand, 7)
    }

    reset() {
        const self = this

        for(let i = 0; i < self.rankCnts.length; i++) {
            self.rankCnts[i] = 0
        }
        for(let i = 0; i < self.suitCnts.length; i++) {
            self.suitCnts[i] = 0
        }
        for(let i = 0; i < self.suitMasks.length; i++) {
            self.suitMasks[i] = 0
        }
    }

    calcHandRank(hand: Hand7, cardsCnt: number): HandRank {
        const self = this

        self.reset()
        
        let handRank: HandRank = MakeHandRank()

        // aggregate the ranks, suits, suit masks
        for(let i: number = 0; i < cardsCnt; i++) {
            const rank: CardRank = GetHand7Rank(hand, i)
            const suit: CardSuit = GetHand7Suit(hand, i)
            if(rank < CardRankSize) {
                self.rankCnts[rank]++
                self.suitCnts[suit]++
                self.suitMasks[suit] |= (1 << rank)
            }
        }

        let m1: number = 0 // mask for single cards
        let m2: number = 0 // mask for the pairs
        let m3: number = 0 // mask for sets
        let m4: number = 0 // mask for four of a kind

        let m2cnt: number = 0 // count of pairs
        let m3cnt: number = 0 // count of sets

        // determine the category based on the rank counts
        for(let i: number = 0; i < CardRankSize; i++) {
            const n = self.rankCnts[i]
            if(n === 0) {
            } else if(n === 1) {
                m1 |= 1 << i
            } else if(n === 2) {
                m2 |= 1 << i
                m2cnt++
            } else if(n === 3) {
                m3 |= 1 << i
                m3cnt++
            } else if(n === 4) {
                m4 |= 1 << i
            }
        }

        let mf: number = 0 // flush mask, 1 bit per card that's in the flush

        for(let i: number = 0; i < CardSuitSize; i++) {
            if(self.suitCnts[i] >= 5) {
                mf = self.suitMasks[i]
                break
            }
        }

        let ms: number

        // if flush then check if it's also straight flush
        // otherwise check for just regular straight
        if(mf !== 0) {
            // possible straight flush
            ms = mf << 1
        } else {
            // possible straight
            // straight is not possible if four of a kind therefore not using m4
            ms = (m1 | m2 | m3) << 1
        }
    
        // copy the ace to position 0
        if((ms & (1 << 13)) !== 0) {
            ms |= 1
        }

        // find the straight high index
        let msi: CardRank = CardRank2
        while(ms >= 31) {
            const msc: CardRank = GetHsb16(ms)
            const mss = ms >> (msc-4)
            if((mss & 31) === 31) {
                msi = msc-1
                break
            }
            ms &= ~(1 << msc)
        }

        if(msi !== CardRank2 && mf !== 0) {
            // straight flush
            handRank = SetCategory(handRank, HandRankCatStraightFlush)
            handRank = SetCardRank(handRank, 4, msi)
            msi--
            handRank = SetCardRank(handRank, 3, msi)
            msi--
            handRank = SetCardRank(handRank, 2, msi)
            msi--
            handRank = SetCardRank(handRank, 1, msi)
            // adjust for an ace
            if(msi === CardRank2) {
                msi = CardRankA
            } else {
                msi--
            }
            handRank = SetCardRank(handRank, 0, msi)
        } else if(m4 !== 0) {
            // four of a kind
            handRank = SetCategory(handRank, HandRankCatQuads)
            const c4: CardRank = GetHsb16(m4)
            handRank = SetCardRank(handRank, 4, c4)
            handRank = SetCardRank(handRank, 3, c4)
            handRank = SetCardRank(handRank, 2, c4)
            handRank = SetCardRank(handRank, 1, c4)
            const c1: CardRank = GetHsb16(m3 | m2 | m1)
            handRank = SetCardRank(handRank, 0, c1)
        } else if(m3cnt === 2) {
            handRank = SetCategory(handRank, HandRankCatFullHouse)
            const c3: CardRank = GetHsb16(m3)
            handRank = SetCardRank(handRank, 4, c3)
            handRank = SetCardRank(handRank, 3, c3)
            handRank = SetCardRank(handRank, 2, c3)
            m3 &= ~(1 << c3)
            const c2: CardRank = GetHsb16(m3)
            handRank = SetCardRank(handRank, 1, c2)
            handRank = SetCardRank(handRank, 0, c2)
        } else if(m3cnt === 1 && m2cnt > 0) {
            handRank = SetCategory(handRank, HandRankCatFullHouse)
            const c3: CardRank = GetHsb16(m3)
            handRank = SetCardRank(handRank, 4, c3)
            handRank = SetCardRank(handRank, 3, c3)
            handRank = SetCardRank(handRank, 2, c3)
            const c2: CardRank = GetHsb16(m2)
            handRank = SetCardRank(handRank, 1, c2)
            handRank = SetCardRank(handRank, 0, c2)
        } else if(mf !== 0) {
            handRank = SetCategory(handRank, HandRankCatFlush)
            let c: CardRank = GetHsb16(mf)
            handRank = SetCardRank(handRank, 4, c)
            mf &= ~(1 << c)
            c = GetHsb16(mf)
            handRank = SetCardRank(handRank, 3, c)
            mf &= ~(1 << c)
            c = GetHsb16(mf)
            handRank = SetCardRank(handRank, 2, c)
            mf &= ~(1 << c)
            c = GetHsb16(mf)
            handRank = SetCardRank(handRank, 1, c)
            mf &= ~(1 << c)
            c = GetHsb16(mf)
            handRank = SetCardRank(handRank, 0, c)
        } else if(msi !== 0) {
            // straight
            handRank = SetCategory(handRank, HandRankCatStraight)
            handRank = SetCardRank(handRank, 4, msi)
            msi--
            handRank = SetCardRank(handRank, 3, msi)
            msi--
            handRank = SetCardRank(handRank, 2, msi)
            msi--
            handRank = SetCardRank(handRank, 1, msi)
            // adjust for an ace
            if(msi === 0) {
                msi = CardRankA
            } else {
                msi--
            }
            handRank = SetCardRank(handRank, 0, msi)
        } else if(m3 !== 0) {
            handRank = SetCategory(handRank, HandRankCatSet)
            const c3: CardRank = GetHsb16(m3)
            handRank = SetCardRank(handRank, 4, c3)
            handRank = SetCardRank(handRank, 3, c3)
            handRank = SetCardRank(handRank, 2, c3)
            // m2 must be zero, so try only m1
            let c1: CardRank = GetHsb16(m1)
            handRank = SetCardRank(handRank, 1, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 0, c1)
        } else if(m2cnt > 1) {
            handRank = SetCategory(handRank, HandRankCatTwoPair)
            let c2: CardRank = GetHsb16(m2)
            handRank = SetCardRank(handRank, 4, c2)
            handRank = SetCardRank(handRank, 3, c2)
            m2 &= ~(1 << c2)
            c2 = GetHsb16(m2)
            handRank = SetCardRank(handRank, 2, c2)
            handRank = SetCardRank(handRank, 1, c2)
            m2 &= ~(1 << c2)
            const c1: CardRank = GetHsb16(m1 | m2)
            handRank = SetCardRank(handRank, 0, c1)
        } else if(m2cnt > 0) {
            handRank = SetCategory(handRank, HandRankCatOnePair)
            const c2: CardRank = GetHsb16(m2)
            handRank = SetCardRank(handRank, 4, c2)
            handRank = SetCardRank(handRank, 3, c2)
            let c1: CardRank = GetHsb16(m1)
            handRank = SetCardRank(handRank, 2, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 1, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 0, c1)
        } else {
            handRank = SetCategory(handRank, HandRankCatHighCard)
            let c1: CardRank = GetHsb16(m1)
            handRank = SetCardRank(handRank, 4, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 3, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 2, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 1, c1)
            m1 &= ~(1 << c1)
            c1 = GetHsb16(m1)
            handRank = SetCardRank(handRank, 0, c1)            
        }
        
        return handRank
    }    
}
