import React, { Component } from 'react'

import { WorkersOptions } from './WorkersOptions'

import Select from 'react-select'

export default class WorkersSetting extends Component {

  constructor(props) {
    super(props)

    this.options = []
    Object.keys(WorkersOptions).map(key => this.options.push(WorkersOptions[key]))
  }

  handleChange = selectedOption => {
    this.props.setWorkers(selectedOption.value)
  }

  customStyles = {
    option: (provided, state) => ({
      ...provided,
      textAlign: "right",
    }),
    control: (provided) => ({
      ...provided,
    }),
    singleValue: (provided, state) => ({
      ...provided,
    }),
  }

  render() {
    const selectedOption = WorkersOptions[this.props.workers.toString()]

    return (
      <span style={{width: 150}}>
        <Select
          styles={this.customStyles}
          value={selectedOption}
          onChange={this.handleChange}
          options={this.options}
        />
      </span>
    )
  }
}
