import React, { Component } from 'react'

import isMobileDevice from '../../utils/isMobileDevice'

import { codeIsNormal, codeIsPlayers, text2code, code2text, code2html, code2symbol, code2color } from '../../utils/cards'

import css from './Card.module.css'

const imd = isMobileDevice()

export default class Card extends Component {

  constructor(props) {
    super(props)
    this.code0 = ""
    this.comp0 = null
  }

  render0() {
    const props = this.props
    const style = {...props.style}||{}
    const html = props.html
    const vertical = props.vertical
    const ball = props.ball
    let grey = false
    let card = props.card || props.children
    let code = props.code !== undefined ? props.code : text2code(card)

    if(props.handArr) {
      if((codeIsNormal(code) || codeIsPlayers(code)) && props.handArr.includes(code)) {
        grey = true
      }
    }
    card = code2text(code)
    const symbol = html ? code2html(code, vertical) : code2symbol(code)

    style.color = grey ? "gainsboro" : code2color(code);

    let comp

    const cn = (ball?(html?css.htmlBall:css.ball):(html?(imd?css.htmlSquareMobile:css.htmlSquare):css.square))

    if(props.click) {
      comp = <button 
        className={cn}
        name={card}
        title={card}
        alt={card}
        onClick={props.click.bind(this, card)}
        style={style}
      >
        {symbol}
      </button>
    } else {
      comp = <span
        className={cn}
        name={card}
        title={card}
        alt={card}
        style={style}
      >
        {symbol}
      </span>
    }

    return comp
  }

  render() {
    const props = this.props
    const card = props.card || props.children
    const code = props.code !== undefined ? props.code : text2code(card)
    const handArr = props.handArr
    if(!this.comp0 || this.code0 !== code || this.handArr0 !== handArr) {
      this.code0 = code
      this.handArr0 = handArr
      this.comp0 = this.render0()
    }
    return this.comp0
  }
}
