import { createStore } from 'redux'
import { createEngine } from 'duxen'
import schema from '../modules/App/AppSchema'
import { AppName, AppVersion } from '../modules/App/AppSchema'
import AppInit from '../modules/App/AppInit'
import { loadState, saveState } from './persist'

import mergeDeep from '../utils/mergeDeep'

let initState
const appId = AppName + ":" + AppVersion

const makeStore = (engine) => {

  const reducer = engine.repoReducer()
  const store = createStore(reducer)

  initState = engine.head(store.getState())
  const persistedState = loadState(appId)

  if(persistedState) {
    const mergedState = mergeDeep(initState.toJS(), persistedState)
    store.dispatch(engine.actionFactory().init(mergedState))
  }
  store.dispatch(engine.actionFactory().refresh())
  // store.subscribe(() => saveState(appId, store.getState()))
  // engine.subscribe((action) => console.log("ACTION", Date(), action.type, JSON.stringify(action, undefined, 4)))
  // store.subscribe(() => console.log("REDUX STATE", Date(), JSON.stringify(engine.printableState(getState()).toJS(), undefined, 4)))

  return store
}

export const engine = createEngine(schema)
export const actionFactory = engine.actionFactory()
export const store = makeStore(engine)
export const dispatch = (action) => store.dispatch(action)
export const getState = () => engine.head(store.getState())
export const persist = () => store.subscribe(() => saveState(appId, store.getState()))

AppInit(engine, store, initState)
