// @flow

import type { Card } from './card'
import type { Hand7 } from './hand'
import type { HandCalculator } from './handCalculator'

import { CardWildCard } from './card'
import { MakeHand7, SetHand7Card, GetHand7Card, StringToHand7 } from './hand'
import { Deck } from './deck'

const MaxPlayers: number = 9

export type HandEvalRequest = {
	nsims: number,
	players: number,

	card1: Card,
	card2: Card,
	
	board1: Card,
	board2: Card,
	board3: Card,
	board4: Card,
	board5: Card,
}

export type HandEvalResult = {
	request: HandEvalRequest,
	err: string,

	finalPlayers: number,
	games: number,
	wins: number,
	ties: number,
	pties: number, // pot ties, number of ties expressed as number of pots
	losses: number,
}

export function MakeHandEvalRequest(nsims: number, players: number): HandEvalRequest {
	return {
        nsims: nsims,
        players: players,

        card1: CardWildCard,
        card2: CardWildCard,
        
        board1: CardWildCard,
        board2: CardWildCard,
        board3: CardWildCard,
        board4: CardWildCard,
        board5: CardWildCard,
    }
}

export function MakeHandEvalResult(handEvalRequest: HandEvalRequest, err: string): HandEvalResult {
	return {
		request: handEvalRequest,
		err: err,

		finalPlayers: 0,
		games: 0,
		wins: 0,
		ties: 0,
		pties: 0, // pot ties, number of ties expressed as number of pots
		losses: 0,
	
		wp: 0,
		wo: 0,
		er: 0,
	}
}

export function HandEvalRequestSetHand(evalRequest: HandEvalRequest, handStr: string): string {
	let result: [Hand7, string] = StringToHand7(handStr)
    let hand = result[0]
    let err = result[1]
	if(err) {
		return err
	}

	evalRequest.card1 = GetHand7Card(hand, 0)
	evalRequest.card2 = GetHand7Card(hand, 1)
	
	evalRequest.board1 = GetHand7Card(hand, 2)
	evalRequest.board2 = GetHand7Card(hand, 3)
	evalRequest.board3 = GetHand7Card(hand, 4)
	evalRequest.board4 = GetHand7Card(hand, 5)
	evalRequest.board5 = GetHand7Card(hand, 6)
	return ""
}

export class HandEvaluator {

	sleep(ms: number): Promise<void> {
		return new Promise(resolve => setTimeout(resolve, ms));
	}
		
	async eval(handCalculator: HandCalculator, request: HandEvalRequest, result: HandEvalResult): Promise<void>
	{	
		let h = MakeHand7()
		let wins = 0
		let ties = 0
		let pties = 0 // proportional ties, divided by number of splitting players
		let losses = 0
		const deck = new Deck()
		const nsims = request.nsims
		const players = request.players

		if(!(2 <= players && players <= MaxPlayers)) {
			result.err = "invalid number of players"
			return
		}

		await this.sleep(0)

		for(let n = 0; n < nsims; n++) {
			if((n & 131071) === 0) {
				await this.sleep(0)
			}
			deck.mix()
			deck.removeCard(request.card1)
			deck.removeCard(request.card2)
			deck.removeCard(request.board1)
			deck.removeCard(request.board2)
			deck.removeCard(request.board3)
			deck.removeCard(request.board4)
			deck.removeCard(request.board5)
			
			// deal the cards
			const card1 = request.card1 === CardWildCard ? deck.getNextCard() : request.card1
			const card2 = request.card2 === CardWildCard ? deck.getNextCard() : request.card2
			
			// deal the board
			const board1 = request.board1 === CardWildCard ? deck.getNextCard() : request.board1
			const board2 = request.board2 === CardWildCard ? deck.getNextCard() : request.board2
			const board3 = request.board3 === CardWildCard ? deck.getNextCard() : request.board3
			const board4 = request.board4 === CardWildCard ? deck.getNextCard() : request.board4
			const board5 = request.board5 === CardWildCard ? deck.getNextCard() : request.board5

			h = SetHand7Card(h, 2, board1)
			h = SetHand7Card(h, 3, board2)
			h = SetHand7Card(h, 4, board3)
			h = SetHand7Card(h, 5, board4)
			h = SetHand7Card(h, 6, board5)
	
			h = SetHand7Card(h, 0, card1)
			h = SetHand7Card(h, 1, card2)
			let playerHr = handCalculator.CalcHandRank(h)
			let winnerHr = playerHr
			let tieHands = 0

			for(let i = 1; i < players; i++) {				
				h = SetHand7Card(h, 0, deck.getNextCard())
				h = SetHand7Card(h, 1, deck.getNextCard())
				
				const hr = handCalculator.CalcHandRank(h)
				
				if(hr > winnerHr) {
					winnerHr = hr
					tieHands = 0
				} else if(hr === winnerHr) {
					tieHands++
				}
			}
			
			if(playerHr === winnerHr) {
				if(tieHands > 0) {
					ties++
					pties += 1 / (tieHands + 1)
				} else {
					wins++
				}				
			} else {
				// loss
				losses++
			}
		}
	
		result.request = request
		result.games = request.nsims
		result.wins = wins
		result.ties = ties
		result.pties = pties
		result.losses = losses
	}
}
