import type { Schema } from 'duxen'
import { fromJS } from 'immutable-sorted'

import HomeSchema from '../Home/HomeSchema'

export const AppName = "poserjs";
export const AppVersion = "0.0.2";

const getJsonFromUrl = (url) => {
  // eslint-disable-next-line no-restricted-globals
  if(!url) url = location.search
  var query = url.substr(1)
  var result = {}
  query && query.split("&").forEach(function(part) {
    var item = part.split("=")
    result[item[0]] = decodeURIComponent(item[1])
  })
  return result
}

export const AppSchema: Schema = {

  args: {
    type: 'value',
    persistent: false,
    initValue: fromJS(getJsonFromUrl()),
  },

  selectedTabIndex: {
    type: 'value',
    persistent: true,
    initValue: 0,
  },

  home: {
    type: 'schema',
    schema: HomeSchema,
  },

}

export default AppSchema
