// @flow

import type { CalcType, PoserApiInterface, ApiOptions } from "./poserApi"
import type { HandCalcResp, HandEvalResp, HandEvalPlayersResp, ErrResp } from "./poserApi"

import { MakeErrResp } from './poserApi'

type WorkerReq = {
    api: string,
    id: number,
    apiOptions: ApiOptions,
    args: Array<mixed>,
}

type WorkerResp = {
    api: string,
    id: number,
    args: Array<mixed>,
    result: HandCalcResp | HandEvalResp | HandEvalPlayersResp | ErrResp
}

export class WorkerApi implements PoserApiInterface {
    id: number
    promises: {[number]: any}
    worker: Worker
    workerScriptUrl: string

    constructor(workerScriptUrl: string) {
        this.workerScriptUrl = workerScriptUrl
        this.id = 0
        this.promises = {}
    }

	sleep(ms: number): Promise<void> {
		return new Promise(resolve => setTimeout(resolve, ms));
	}

    checkCalcType(calcType: CalcType) {
        switch(calcType) {
            case "basic":
            case "loki":
                break
            case "fast":
                throw new Error("Calculator not supported in web browser: "+calcType)
            default:
                throw new Error("Unknown calculator type: "+calcType)
        }
    }

    async _setup(): Promise<void> {
        if(this.worker) {
            // The module has been already setup
            await this.sleep(0)
        } else {
            this.worker = new Worker(this.workerScriptUrl)
            this.worker.onmessage = (e) => {
                const data = ((e.data: any): WorkerResp)
                // received a response
                const id = data.id
                const resolve = this.promises[id]
                delete this.promises[id]
                resolve(data.result)
            }
        }
    }

    post(api: string, apiOptions: ApiOptions, args: Array<mixed>): Promise<WorkerResp> {
        return new Promise(resolve => {
            // send a request
            if(this.worker) {
                this.promises[this.id] = resolve
                const workerReq: WorkerReq = {api, id: this.id, apiOptions, args: [...args]}
                this.worker.postMessage(workerReq)
                this.id++
            }
        })
    }

    async HandCalc(apiOptions: ApiOptions, handStr: string, calcType: CalcType): Promise<HandCalcResp | ErrResp> {
        try {
            this.checkCalcType(calcType)
            await this._setup()
            const resp = await this.post("HandCalc", apiOptions, [handStr, calcType])
            return resp
        } catch(ex) {
            return MakeErrResp(ex.toString())
        }
    }
	
    async HandEval(apiOptions: ApiOptions, nsims: number, players: number, handStr: string, calcType: CalcType): Promise<HandEvalResp | ErrResp> {
        try {
            this.checkCalcType(calcType)
            await this._setup()
            const resp = await this.post("HandEval", apiOptions, [nsims, players, handStr, calcType])
            return resp
        } catch(ex) {
            return MakeErrResp(ex.toString())
        }
    }

    async HandEvalPlayers(apiOptions: ApiOptions, nsims: number, handStr: string, calcType: CalcType): Promise<HandEvalPlayersResp | ErrResp> {
        try {
            this.checkCalcType(calcType)
            await this._setup()
            const resp = await this.post("HandEvalPlayers", apiOptions, [nsims, handStr, calcType])
            return resp
        } catch(ex) {
            return MakeErrResp(ex.toString())
        }
    }
}
