import React from 'react'

import Card from './Card'
import Category from './Category'

const EvalResult = (props) => {
  const style = {fontSize:18, height: 26}
  const handResult = props.handResult
  const handEval = props.handEval
  const handCardsCnt = props.handCardsCnt
  const settings = props.settings
  let err
  if(props.err) {
    err = <Category style={style} cat={"error"}>{props.err}</Category>
  } else if(handCardsCnt < 2) {
    err = <Category style={style} cat={"1"}>{"Enter more cards ..."}</Category>
  } else if(!props.handValid) {
    err = <Category style={style} cat={"1"}>{"Enter missing cards"}</Category>
  } else if(props.tanking && props.settings.get("nSims") >= 500000) {
    err = <Category style={style} cat={"F"}>{"Tanking ..."}</Category>
  } else if(handResult.isEmpty()) {
    err = <Category style={style} cat={"error"}>{"no result"}</Category>
  } else if(!handResult.get("handValid")) {
    err = <Category style={style} cat={"error"}>{handResult.get("handErr")}</Category>
  }

  const er = handEval.get("er")
  const wp = handEval.get("wp")
  const wo = handEval.get("wo")
  const cat = er >= 0.25 ? "3" : er <= -0.25 ? "F" : er >= 0 ? "1" : "2"

  return (<>
  {err?err:
  <div>
    <Card title="Hole Card1" style={style} html={true}>{handResult.get("hole").substr(0, 2)}</Card>
    <Card title="Hole Card2" style={style} html={true}>{handResult.get("hole").substr(2, 2)}</Card>
    &nbsp;
    {handCardsCnt >= 5 ? <Category title="Category" style={style} cat={handResult.get("handCat")}>{handResult.get("handCategory")}</Category>:null}
    &nbsp;
    {handCardsCnt >= 5 ? <Category title="Result" style={style} cat={handResult.get("handCat")}>{handResult.get("handResult")}</Category>:null}
    &nbsp;
    {settings.get("latency") ? <Category title="Latency" style={style} cat="1">{Math.floor(props.tankingMs/10)/100}s</Category>:null}
  </div>
  }
  <div>
    <Category title="Return value (%)" style={style} cat={cat}>ER {parseFloat(er*100).toFixed(0)}%</Category>
    &nbsp;
    <Category title="Win probabilty (%)" style={style} cat={cat}>WP {parseFloat(wp*100).toFixed(0)}%</Category>
    &nbsp;
    <Category title="Win odds (loss : win) presented as n : 1" style={style} cat={cat}>WO {wo > 1000 ? ">100" : wo > 10 ? parseFloat(wo).toFixed(0) : parseFloat(wo).toFixed(2)}:1</Category>
  </div>
  </>)
}

export default EvalResult
