import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import './TimeMachineControls.css'

export default class TimeMachineControls extends Component {

  constructor(props) {
    super(props);
    this.state = {
      currentBranch: props.currentBranch,
      live: props.live,
      advanced: false,
      showRepo: props.showRepo,
      saveAs: "",
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      currentBranch: nextProps.currentBranch,
      live: nextProps.live,
      showRepo: nextProps.showRepo,
    });
  }

  onSubmit(e) {
    e.preventDefault()
  }

  onChange(e) {
    this.setState({[e.target.name]: e.target.value})
  }

  onChangeBranch(e) {
    const { switchBranch } = this.props
    const currentBranch = e.target.value
    this.setState({currentBranch: currentBranch})
    switchBranch(currentBranch)
  }

  onSaveBranch(e) {
    const { saveBranch } = this.props
    if(this.state.saveAs) {
      saveBranch(this.state.saveAs)
    }
    this.setState({"saveAs": ""})
  }

  onReset(e) {
    const { currentBranch, resetBranch } = this.props
    resetBranch(currentBranch)
  }

  onRemove(e) {
    const { currentBranch, removeBranch } = this.props
    removeBranch(currentBranch)
  }

  onGoFastForward(e) {
    const { goForward } = this.props
    goForward(10)
  }

  onGoForward(e) {
    const { goForward } = this.props
    goForward(1)
  }

  onGoBack(e) {
    const { goBack } = this.props
    goBack(1)
  }

  onGoFastBack(e) {
    const { goBack } = this.props
    goBack(10)
  }

  onGoLive(e) {
    const { goLive } = this.props
    goLive()
  }

  toggleLive(e) {
    const { goBack, goLive } = this.props
    const live = !this.state.live
    this.setState({live: live})
    if(live) {
      goLive()
    } else {
      goBack(0)
    }
  }

  toggleAdvanced(e) {
    const advanced = !this.state.advanced
    this.setState({advanced: advanced})
  }

  toggleRepo(e) {
    const { doShowRepo } = this.props
    const showRepo = !this.state.showRepo
    this.setState({showRepo: showRepo})
    doShowRepo(showRepo)
  }

  onSliderChange(value) {
    const { goForward, goBack, currentIndex } = this.props
    const index = value - 1;
    if(index > currentIndex) {
      goForward(index-currentIndex);
    } else if(index < currentIndex) {
      goBack(currentIndex-index);
    }
  }

  render() {
    const { branches, currentIndex, maxIndex } = this.props
    let input

    return (
      <div>
      <form onSubmit={this.onSubmit.bind(this)}>
        <button onClick={this.toggleLive.bind(this)} title={this.state.live?"Time Machine On":"Time Machine Off"}>
        {this.state.live 
        ? <FontAwesomeIcon icon="history"/>
        : <FontAwesomeIcon icon="play"/>}
        </button>
        <button onClick={this.toggleAdvanced.bind(this)} title={this.state.advanced ? "Advanced Mode Off" : "Advanced Mode On"}>
          {this.state.advanced ? <b>&#x2630;</b> : <span>&#x2630;</span>}
        </button>
        &nbsp;
        {this.state.advanced ? [
          <button key={1} onClick={this.toggleRepo.bind(this)} title={this.state.showRepo?"Show State":"Show Repo"}>
          {this.state.showRepo 
          ? <FontAwesomeIcon key={2} icon="genderless"/>
          : <FontAwesomeIcon key={2} icon="code-branch"/>}
          </button>,         
          <select key={3} 
            name="currentBranch"
            value={this.state.currentBranch}
            onChange={this.onChangeBranch.bind(this)}
            title="Change Branch"
          >
            {branches.map((branch, id) =>
              (<option key={id} value={branch}>{branch}</option>)
            ).toIndexedSeq()}
        </select>] : null }
        <button onClick={this.onGoBack.bind(this)} title="Go Back">
          &lt;
        </button>
        &nbsp;{currentIndex+1} / {maxIndex+1}&nbsp;
        <button onClick={this.onGoForward.bind(this)} title="Go Forward">
          &gt;
        </button>
        {this.state.advanced ?
        <span>
        <input
          name="saveAs"
          size="8"
          value={this.state.saveAs}
          onChange={this.onChange.bind(this)}/>
        <button onClick={this.onSaveBranch.bind(this)} title="Save Branch"
          disabled={!this.state.saveAs}>
          Save
        </button>
        <button onClick={this.onReset.bind(this)} title="Reset Branch">
          Reset
        </button>
        <button onClick={this.onRemove.bind(this)} title="Remove Branch">
          Remove
        </button>
        </span> : null}
      </form>
      {this.state.live ? null : <div className="TimeMachineControls-slider"><Slider
        min={1}
        max={maxIndex+1}
        value={currentIndex+1}
        step={1}
        onChange={this.onSliderChange.bind(this)}
      />
      </div>}
      </div>
    )
  }
}

TimeMachineControls.propTypes = {
  currentBranch: PropTypes.string.isRequired,
  currentIndex: PropTypes.number.isRequired,
  maxIndex: PropTypes.number.isRequired,
  live: PropTypes.bool.isRequired,
  branches: PropTypes.object.isRequired,
  goBack: PropTypes.func.isRequired,
  goForward: PropTypes.func.isRequired,
  goLive: PropTypes.func.isRequired,
  switchBranch: PropTypes.func.isRequired,
  saveBranch: PropTypes.func.isRequired,
  resetBranch: PropTypes.func.isRequired,
  removeBranch: PropTypes.func.isRequired,
}
